.b-notific {
  &__item {
    padding: 12px;
    border-radius: 6px;
    border: 1px solid var(--grays-200, #DADDE1);
    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }
  &__main {
    display: flex;
    align-items: center;
  }
  &__img {
    width: 40px;
    margin-right: 12px;
  }
  &__title {
    color: var(--grays-900, #1C2229);
    font-size: 15px;
    font-weight: 500;
    line-height: 144%;
  }
  &__info {
    color: var(--grays-500, #6C7685);
    font-size: 12px;
    line-height: 125%;
  }
  &__note {
    color: var(--grays-600, #475467);
    font-size: 13px;
    line-height: 140%;
  }
  &__plug {
    width: 280px;
    font-weight: normal;

    .ic {
      margin-right: 8px;
      fill: #16B364;
      stroke: none;
    }

    &.active {
      font-size: 13px;
      line-height: 1;
      color: var(--success-800, #0D6B3C);
    }
  }

  &__group {
    &:not(:last-child) {
      margin-bottom: 28px;
      padding-bottom: 28px;
      border-bottom: 1px solid var(--grays-100, #EDEEF0);
    }

    .custom-checkbox:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  &__group-title {
    margin-bottom: 16px;
    font-size: 16px;
    font-weight: 500;
    line-height: 140%;
  }

  &__switch {
    display: flex;
    align-items: center;
    max-width: 500px;

    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }
  &__switch-title {
    padding-left: 20px;
  }
  &__link {
    color: var(--Grays-500, #6C7685);
    font-size: 13px;
    text-decoration-line: underline;
    border: none;
    background-color: transparent;
  }

  &__footer {
    display: flex;
    align-items: center;
    margin: 12px -12px -12px;
    border-top: 1px solid var(--Grays-100, #EDEEF0);
  }
  &__footer-item {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 0 0 auto;
    width: 50%;
    padding: 12px;
    text-align: center;

    &:first-child {
      border-right: 1px solid var(--Grays-100, #EDEEF0);
    }
    .b-notific__plug {
      color: var(--Grays-500, #6C7685);
      font-size: 13px;
      line-height: 140%;
      .ic {
        stroke: #6C7685;
      }
    }
  }

  .ui-date-picker-wrap {
    width: 96px;
    margin-left: 12px;
  }
  .ui-date-picker {
    padding: 8px 12px;
  }

  @include media-breakpoint-down(sm) {
    &__main {
      margin-bottom: 8px;
    }
    &__plug {
      width: 100%;
    }
  }
}


.b-notific-group {
  display: flex;
  align-items: center;
  margin-bottom: 28px;
  &__item {
    display: flex;
    align-items: center;
    &:not(:last-child) {
      margin-right: 40px;
    }
    .ic {
      margin-left: 12px;
    }
  }

  @include media-breakpoint-down(sm) {
    display: block;

    &__item {
      justify-content: space-between;
      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 20px;
      }
    }
  }
}


.b-notific-group2 {
  display: flex;
  align-items: center;
  &:not(:last-child) {
    margin-bottom: 24px;
  }

  &__checkbox {
    width: 140px;
    flex: 0 0 auto;
  }
  &__select {
    width: 340px;
    flex: 0 0 auto;
  }
  .ui-date-picker-wrap {
    flex: 0 0 auto;
  }

  @include media-breakpoint-down(sm) {
    display: block;
    &:not(:last-child) {
      margin-bottom: 40px;
    }
    &__select {
      width: 100%;
      margin-top: 8px;
    }
    .ui-date-picker-wrap {
      margin-left: 0;
      margin-top: 8px;
    }
  }
}
