.b-modal-templates {
  &__sidebar {
    flex: 0 0 auto;
    width: 288px;
    max-height: calc(100vh - 220px);
    padding: 28px 16px;
    border-right: 1px solid #EDEEF0;
    overflow-y: auto;
  }
  &__main {
    max-height: calc(100vh - 220px);
    flex: 0 0 auto;
    width: calc(100% - 288px);
    padding: 20px 24px 0;
    overflow-y: auto;
  }
  &__title {
    font-weight: 500;
    font-size: 18px;
    line-height: 140%;
    color: #1C2229;
  }
  &__text {
    margin-bottom: 22px;
    font-size: 14px;
    line-height: 140%;
    color: #6C7685;
  }
  &__group {
    display: flex;
    flex-wrap: wrap;
    margin-right: -12px;
  }
  &__item {
    display: flex;
    width: calc(33.33% - 12px);
    flex: 0 0 auto;
    flex-wrap: wrap;
    align-content: flex-start;
    margin-right: 12px;
    margin-bottom: 14px;
    padding: 0;
    border: 1px solid #DADDE1;
    border-radius: 6px;
    background-color: #FFF;
    text-align: left;
    overflow: hidden;
  }
  &__img {
    width: 100%;
    height: 132px;
    object-fit: cover;
  }
  &__inner {
    padding: 12px;
  }
  &__name {
    font-weight: 500;
    font-size: 15px;
    line-height: 122%;
    color: #2B323E;
  }
  &__descrip {
    margin-top: 2px;
    font-size: 11px;
    line-height: 125%;
    color: #6C7685;
  }
  &__nav {
    .nav-item {
      width: 100%;
      margin-bottom: 2px;
    }
    .nav-link {
      display: flex;
      align-items: center;
      padding: 6px 6px 6px 8px;
      justify-content: space-between;
      font-size: 15px;
      line-height: 144%;
      color: #394352;
      border-radius: 5px;
      &.active {
        background: #F3F7F3;
        color: #2F8733;

        .b-modal-templates__nav-number {
          color: #2F8733;
        }
      }
    }
  }
  &__nav-name {}
  &__nav-number {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 21px;
    padding: 4px;
    background: #EAF3EB;
    border-radius: 3px;
    font-size: 10px;
    line-height: 125%;
  }

  .modal-dialog {
    max-width: 1090px;
    margin-top: 80px;
  }
  &__body {
    display: flex;
  }


  @include media-breakpoint-down(md) {
    &__item {
      width: calc(50% - 12px);
    }
  }

  @include media-breakpoint-down(sm) {
    &__body {
      display: block;
    }

    &__item {
      width: 100%;
    }
    &__title {
      margin-bottom: 4px;
    }
    &__main {
      width: 100%;
      max-height: calc(100vh - 175px);
      padding: 10px 14px;
    }
    &__sidebar {
      width: 100%;
      white-space: nowrap;
      padding: 10px;
      &::-webkit-scrollbar {
        height: 2px;
      }
    }
    &__nav-number {
      display: none;
    }
    &__nav {
      flex-wrap: nowrap;
      .nav-item {
        width: auto;
        margin-right: 8px;
      }
    }
    .modal-dialog {
      margin: 54px 0 0;
    }
    .modal-content {
      border-radius: 0;
    }
  }
}
