.bs-messenger {
  position: fixed;
  bottom: 32px;
  right: 32px;
  font-family: Roboto;
  z-index: 9999;
  &__trigger {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px;
    border: none;
    background: #538CE4;

    .ic {
      width: 24px;
      height: 24px;
    }

    .bs-messenger__header {
      padding: 0 12px 0 0;
    }
  }
  &__dropdown {
    position: absolute;
    bottom: 68px;
    right: 0;
    display: none;
    width: 320px;
    background-color: #FFF;
    box-shadow: 0px 5px 8px -4px rgba(0, 0, 0, 0.03), 0px 15px 24px -4px rgba(0, 0, 0, 0.04), 0px 4px 20px 0px rgba(31, 34, 41, 0.06);
  }
  &__header {
    display: flex;
    align-items: center;
    padding: 12px;
    background: #538CE4;
    color: #FFF;
    color: var(--White, #FFF);
    font-size: 15px;
    font-weight: 500;
    line-height: 144%;
  }
  &__logo {
    margin-right: 12px;
  }
  &__body {
    max-height: calc(100vh - 280px);
    padding: 12px;
    overflow-x: hidden;
    overflow-y: auto;
    scrollbar-color: #D0D5DD transparent;
    scrollbar-width: thin;
    &::-webkit-scrollbar {
      -webkit-appearance: none;
    }

    &::-webkit-scrollbar:vertical {
      width: 4px;
    }

    &::-webkit-scrollbar-thumb {
      background: #D0D5DD;
      border-radius: 2px;
    }
  }
  &__item {
    margin-bottom: 16px;
    &.st-left {
      .bs-messenger__info {
        border-bottom-left-radius: 0;
        background: var(--grays-200, #DADDE1);
      }
    }
    &.st-right {
      text-align: right;
      .bs-messenger__info {
        border-bottom-right-radius: 0;
        background: #538CE4;
        color: #FFF;
      }
    }
  }
  &__time {
    display: block;
    margin-bottom: 4px;
    color: var(--grays-600, #475467);
    font-size: 12px;
    line-height: 125%;
  }
  &__author {
    display: flex;
    align-items: center;
    margin-top: 8px;
    color: var(--grays-500, #6C7685);
    font-size: 14px;
    line-height: 125%;
  }
  &__author-photo {
    width: 24px;
    height: 24px;
    margin-right: 8px;
    object-fit: cover;
    border-radius: 50%;
    overflow: hidden;
  }
  &__info {
    display: inline-block;
    max-width: 90%;
    padding: 12px;
    color: var(--grays-800, #2B323E);
    font-size: 15px;
    line-height: 144%;
    border-radius: 12px;
    text-align: left;
  }
  &__footer {
    padding: 8px;
    border-top: 1px solid var(--grays-50, #F5F7FA);
    background: var(--grays-25, #F9FAFB);
  }
  &__form {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__input {
    height: 40px;
    flex-grow: 1;
    padding: 10px;
    border: none;
    background-color: transparent;

    &::placeholder {
      color: var(--grays-400, #9198A4);
    }
  }
  &__submit {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    margin-left: 4px;
    border: none;
    background-color: transparent;
  }
  &__text {
    padding: 4px;
    text-align: center;
    color: var(--grays-400, #9198A4);
    font-size: 14px;
    line-height: 125%;
  }
  &__editing {
    width: 24px;
    height: 24px;
  }

  &__btn-item {
    &:not(:last-child) {
      margin-bottom: 6px;
    }
  }

  &__btn {
    display: inline-block;
    padding: 6px 12px;
    color: var(--grays-500, #6C7685);
    font-size: 14px;
    line-height: 125%;
    border-radius: 4px;
    border: 1px solid var(--grays-200, #DADDE1);
    background-color: #FFF;
    transition: all .3s;

    &:hover {
      background-color: #F5F7FA;
      border-color: #475467;
      color: #475467;
    }
  }

  &.st-show & {
    &__dropdown {
      display: block;
    }
    &__trigger {
      .bs-messenger__header {
        display: none;
      }
    }
  }


  @media (max-width:767.98px) {
    right: 16px;
    bottom: 16px;
    // &__trigger {
    //   .ic {
    //     width: 24px;
    //     height: 24px;
    //   }
    // }
  }
}

.bs-messenger-registr {
  margin-bottom: 24px;
  &__inner {
    padding: 16px;
    border-radius: 8px;
    background: var(--grays-50, #F5F7FA);
  }
  &__info {
    margin-bottom: 16px;
    color: var(--grays-800, #2B323E);
    font-size: 15px;
    line-height: 144%;
  }
  &__group {
    margin-bottom: 8px;
  }
  &__input {
    width: 100%;
    padding: 8px 12px;
    border-radius: 4px;
    border: 1px solid var(--Stroke, #D0D3D8);
    background-color: #FFF;
    font-size: 14px;
    color: var(--Dark, #111722);

    &::placeholder {
      color: var(--Dark, #111722);
      opacity: 0.4;
    }
  }
  &__btns {
    display: flex;
    justify-content: space-between;

    .bs-messenger-registr__btn {
      width: calc(50% - 4px);
    }
  }
  &__btn {
    display: block;
    width: 100%;
    margin-top: 16px;
    padding: 8px 16px;
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
    border-radius: 4px;
    border: 1px solid;
    transition: all .3s;
  }
  &__btn-primary {
    background: #538CE4;
    border-color: #538CE4;
    color: #FFF;
    &:hover {
      color: #fff;
      background-color: #004bdc;
      border-color: #0047cf;
    }
  }
  &__btn-secondary {
    background-color: transparent;
    color: var(--grays-600, #475467);
    border-color: var(--secondary-stroke, #D6D6D6);
    &:hover {
      background-color: #F5F7FA;
      border-color: #475467;
      color: #475467;
      stroke: #475467;
    }
  }
  &__text {
    padding: 8px 12px 0;
    color: var(--grays-600, #475467);
    font-size: 13px;
    line-height: 140%;
  }
}
