.b-preview {
  position: relative;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 24px;

  &__note {
    margin-bottom: 8px;
    padding: 10px;
    text-align: center;
    color: var(--grays-700, #394352);
    font-size: 13px;
    a {
      text-decoration: underline;
      color: var(--grays-700, #394352);
    }
  }

  &__footer {
    margin-top: 24px;
  }

  &__header {
    .ui-language {
      position: absolute;
      top: 10px;
      right: 0;
    }
  }

  .b-frame {
    &__body {
      max-width: 768px;
      margin-left: auto;
      margin-right: auto;
    }
    &__item {
      border: none;
    }
    &__title {
      font-size: 22px;
      font-weight: 500;
    }
    &__title-wrap {
      max-width: 100%;
    }
    &__desrip {
      margin-top: 10px;
      margin-bottom: 40px;
      color: var(--grays-700, #394352);
      font-size: 18px;
      line-height: 1.4;
    }
    &__desrip-wrap {
      min-height: 0;
    }
    &__input-wrap {
      margin-top: 12px;
    }
    &__content {
      &_contacts {
        .b-frame__title {
          margin-bottom: 0;
          font-size: 16px;
          font-weight: normal;
        }
      }
    }
    &__content-inner {
      padding-top: 40px;

      .label-note {
        position: relative;
        top: -10px;
        font-size: 0.875rem;
        padding-left: 4px;
      }
    }
    &__area-wrap {
      width: 100%;
    }
  }


  .b-frame-icons {
    &__labels {
      padding-left: 12px;
      padding-right: 12px;
    }
    &__item {
      &_count{
        &:not(.st-type-a) {
          &:after {
            content: none;
          }
          label {
            position: relative;
            &:after {
              position: absolute;
              top: 53%;
              left: 50%;
              -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
              content: counter(item);
              line-height: 1.2;
            }
          }
        }
      }
    }
  }

  .b-frame-feedback {
    margin-top: 56px;
    &__item {
      padding: 0;
      border: none;
    }
    &__title {
      margin-top: 26px;
    }
    &__logo {
      margin-top: 0;
    }
    &__img {
      margin-top: 0;
    }
  }


  @include media-breakpoint-down(md) {
    padding-top: 40px;

    &__header {
      .ui-language {
        top: 8px;
        right: 8px;
        &:not(.ui-language_w-bdr) {
          width: auto;
        }
        .btn {
          font-size: 0;
        }
      }
    }
  }
}
