.b-modal-soc-links {
  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    margin-bottom: 8px;
  }
  &__item {
    margin-bottom: 16px;
    text-align: center;
    border: none;
    background-color: transparent;
    &:not(:last-child) {
      margin-right: 16px;
    }
  }
  &__title {
    display: block;
    margin-top: 8px;
    color: var(--grays-800, #2B323E);
    font-size: 12px;
    font-weight: 400;
    line-height: 125%;
  }


  @include media-breakpoint-down(sm) {
    &__list {
      margin-bottom: 12px;
    }
    &__item {
      width: 100%;
      display: flex;
      align-items: center;
      margin-bottom: 12px;

      &:not(:last-child) {
        margin-right: 0;
      }

      br {
        display: none;
      }
    }
    &__title {
      margin-top: 0;
      font-size: 16px;
    }
    &__ic {
      width: 32px;
      height: 32px;
      margin-right: 8px;
    }
  }
}
