.b-menu {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 768px;
  flex-grow: 1;
  &__title {
    display: flex;
    align-items: center;
    width: 100%;
    min-height: 140px;
    padding-left: 16px;
    padding-right: 16px;
    font-size: 32px;
    font-weight: 700;
    text-align: left;
  }
  &__title-sm {
    display: flex;
    justify-content: center;
    width: 100%;
    min-height: 76px;
    padding-left: 16px;
    padding-right: 16px;
    padding-bottom: 24px;
    font-size: 24px;
    font-weight: 700;
  }
  &__body {
    width: 100%;
    flex-grow: 1;
    margin-top: -24px;
    padding: 16px 16px 90px;
    background-color: #FFF;
    border-radius: 24px 24px 0 0;
  }
  &__footer {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 16px;
    background-color: #FFF;
    z-index: 100;
  }
}

.b-menu-list {
  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 8px;
    padding: 23px 16px;
    text-align: center;
    border: 1px solid #EDEEF0;
    border-radius: 12px;
    background-color: rgba(249, 250, 251, 1);
    text-decoration: none;
    font-size: 22px;
    font-weight: 700;
    color: #1C2229;
    &:hover {
      text-decoration: none;
    }
  }
}


.b-menu-card {
  border: 1px solid #DADDE1;
  border-radius: 12px;
  color: #1C2229;
  font-size: 13px;
  &:not(:last-child) {
    margin-bottom: 16px;
  }
  &__media {
    position: relative;
    min-height: 16px;
    margin-bottom: 12px;
    img {
      width: 100%;
      height: 160px;
      object-fit: cover;
      border-radius: 11px 11px 0 0;
    }
  }
  &__labels {
    position: absolute;
    top: 8px;
    left: 8px;
  }
  &__label {
    margin-right: 4px;
    margin-bottom: 4px;
    padding: 2px 8px 2px 6px;
    background-color: #fff;
    border-radius: 40px;
    .ic {
      width: 14px;
      height: 14px;
      margin-right: 4px;
    }
  }
  &__main {
    padding: 0 12px 12px;
    border-radius: 0 0 11px 11px;
    background-color: #FFF;
  }
  &__title {
    display: block;
    margin-bottom: 8px;
    font-size: 22px;
    font-weight: 700;
    color: #1C2229;
  }
  &__tags {
    color: #475467;
  }
  &__tag {
    display: inline-flex;
    align-items: center;
    margin-right: 16px;
    margin-bottom: 8px;
    font-size: 14px;
    .ic {
      margin-right: 4px;
      stroke: #475467;
    }
  }
  &__info {
    font-size: 14px;
    color: #2B323E;
  }
  &__desrip {
    margin-top: 16px;
  }
  &__desrip-title {
    margin-bottom: 4px;
  }
  &__desrip-list {
    overflow-x: auto;
    white-space: nowrap;
  }
  &__desrip-item {
    display: inline-flex;
    align-items: center;
    margin-right: 4px;
    padding: 4px 16px 4px 4px;
    background-color: #EDEEF0;
    border-radius: 40px;
    .ic {
      width: 28px;
      height: 28px;
      border-radius: 50%;
      overflow: hidden;
      margin-right: 8px;
    }
  }
  &__footer {
    margin-top: 16px;
    padding-top: 12px;
    border-top: 1px solid #EDEEF0;
  }
  &__weight {
    color: #394352;
  }
  &__price {
    font-size: 28px;
    font-weight: 700;
    color: #2F8733;
  }
  &__table {
    padding: 8px 12px;
    border-radius: 8px;
    border: 1px solid #EDEEF0;
  }
  &__table-title {
    margin-bottom: 12px;
    padding-bottom: 8px;
    font-size: 14px;
    font-weight: 500;
    border-bottom: 1px solid #EDEEF0;
  }
  &__table-main {
    width: 100%;
    font-size: 14px;
    td {
      padding: 4px 2px;
      &:first-child {
        width: 1000px;
      }
      &:not(:last-child) {
        padding-right: 24px;
      }
    }
  }
}

