.b-print-page {
  margin: 20mm;
  color: #231F20;
  font-size: 16pt;
  line-height: 1.2;
  font-family: Arial, sans-serif;
  a {
    color: #338C43;
  }
  &__title {
    margin-bottom: 5mm;
    font-size: 30pt;
    font-weight: bold;
  }
  &__info {
    margin-bottom: 3mm;
    font-size: 14pt;
    color: #808285;
    span {
      color: #231F20;
    }
  }
  &__info-gr {
    margin-bottom: 8mm;
  }
  &__bl {
    margin-bottom: 8mm;
    padding: 2mm 2mm 2mm 4.5mm;
    overflow: hidden;
    background-color: #E6E7E8;
    border-radius: 3mm;

    &_sm {
      margin-bottom: 2mm;
      .b-print-page__bl-img {
        width: 24mm;
        height: 24mm;
      }
    }
  }
  &__bl-img {
    float: right;
    width: 30mm;
    height: 30mm;
    margin-left: 20mm;
  }
  &__bl-title {
    margin-top: 1mm;
    margin-bottom: 2mm;
  }
  &__bl-text {
    margin-top: 1mm;
    margin-bottom: 1mm;
    font-size: 14pt;
  }
  &__bl-link {
    font-weight: bold;
  }
  &__login {
    margin-bottom: 15mm;
    padding: 4mm 2mm 1mm 4.5mm;
    border: 1px solid #338C43;
    border-radius: 3mm;
  }
  &__login-text {
    font-size: 14pt;
  }
  &__login-info {
    margin-bottom: 4mm;
    font-weight: bold;
  }
  &__title-2 {
    margin-bottom: 4.5mm;
    font-weight: bold;
  }
  &__section-2 {
    margin-top: 2.5mm;
  }
  &__title-3 {
    margin-top: 16mm;
    margin-bottom: 4mm;
    font-weight: bold;
  }
  &__group {
    width: 100%;
    font-weight: bold;
    font-size: 15pt;
    a {
      font-size: 14pt;
      font-weight: normal;
      word-break: break-all;
    }
    .ic {
      display: block;
      width: 24mm;
      height: 24mm;
      margin-top: 1.5mm;
      margin-bottom: 3.5mm;
    }
    td {
      width: 33.33%;
      padding-right: 16mm;
    }
  }

  &__sheet-inner {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 0 5mm;
    padding-top: 40mm;
    text-align: center;
  }
  &__sheet-wrap {
    width: 100%;
  }
  &__header {
    width: 100%;
    margin-bottom: 40mm;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: left;
    font-size: 26pt;
    font-weight: bold;
  }
  &__header-logo {
    width: 55mm;
    height: auto;
    margin-right: 5mm;
  }
  &__gr-2 {
    margin-bottom: 40mm;
    text-align: center;
    .ic {
      width: 100mm;
      height: 100mm;
      margin-bottom: 10mm;
    }
  }
  &__gr-2-title {
    margin-bottom: 40mm;
    font-weight: bold;
    font-size: 21pt;
  }
  &__gr-2-text {
    font-weight: bold;
    font-size: 17pt;
  }
  &__footer {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    padding-top: 10mm;
    border-top: 1px solid #7d8183;
    font-size: 17pt;
    font-weight: bold;
    .ic {
      margin-right: 1mm;
    }
  }

}

@media print {
  .b-print-page__sheet {page-break-after: always;}
}
