.b-modal-get-code {
  &__title {
    margin-bottom: 6px;
    color: var(--grays-900, #1C2229);
    font-size: 15px;
    font-weight: 500;
    line-height: 144%;
  }
  &__text {
    margin-bottom: 16px;
    color: var(--grays-600, #475467);
  }

  .modal-dialog {
    max-width: 620px;
  }


  @include media-breakpoint-down(sm) {
    .modal-dialog {
      max-width: 100%;
    }
  }
}
