.b-posts {
  display: flex;
  flex-wrap: wrap;
  padding: 20px 8px 4px 24px;
  background: var(--Grays-50, #F5F7FA);
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 432px;
    margin-right: 16px;
    margin-bottom: 16px;
    color: var(--Grays-600, #475467);
    font-size: 14px;
    line-height: 140%;
    border-radius: 6px;
    border: 1px solid var(--Grays-100, #EDEEF0);
    background: var(--White, #FFF);
  }
  &__img {
    width: 100%;
    height: 228px;
    object-fit: cover;
  }
  &__main {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    flex-grow: 1;
    padding: 20px;
  }
  &__inner {
    flex-grow: 1;
  }
  &__tags {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 16px;
  }
  &__tag {
    white-space: nowrap;
    &:not(:first-child) {
      &:before {
        content: '|';
        margin-left: 4px;
        margin-right: 4px;
      }
    }
  }
  &__title {
    margin-bottom: 8px;
    color: var(--Dark, #111722);
    font-size: 20px;
    font-weight: 600;
    line-height: normal;
  }
  &__info {
    margin-bottom: 16px;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  @include media-breakpoint-only(md) {
    &__item {
      width: calc(50% - 16px);
    }
  }
}

.b-post {
  padding-left: 24px;
  padding-right: 24px;
  &__inner {
    max-width: 970px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 44px;
    padding-bottom: 20px;
  }
  img {
    max-width: 100%;
    height: auto;
    margin-top: 32px;
    margin-bottom: 32px;
  }
  p {
    margin-bottom: 0;
    + p {
      margin-bottom: 16px;
    }
  }
}

.b-post-footer {
  display: flex;
  justify-content: space-between;
  padding-top: 56px;
  padding-bottom: 8px;

  @include media-breakpoint-down(sm) {
    display: block;
    .btn {
      width: 100%;
    }
  }
}
