.ui-language {
  &:not(.ui-language_w-bdr) {
    width: 170px;
  }

  .dropdown {
    display: flex;
    justify-content: center;
  }

  .dropdown-toggle {
    display: flex;
    align-items: center;
    background-color: transparent;

    &:not(.ui-select_w-border) {
      width: auto;
      padding: 6px 12px 6px 6px;
      border: none;
      border-radius: 4px;
    }
    &:hover {
      background-color: #F3F7F3;
    }
    .ic {
      margin-right: 8px;
    }
  }
  .dropdown-menu {
    min-width: 0;
    padding: 0;
    border: 1px solid #EDEEF0;
    box-shadow: 0px 4px 20px rgba(31, 34, 41, 0.06), 0px 15px 24px -4px rgba(0, 0, 0, 0.04), 0px 5px 8px -4px rgba(0, 0, 0, 0.03);
    border-radius: 6px;

  }

  .dropdown-item {
    display: flex;
    align-items: center;
    padding: 7px 23px 7px 12px;
    line-height: 1.4;

    &.active,
    &:active {
      background-color: #EAF3EB;
      color: #1C2229;
    }

    &:hover {
      background-color: #F3F7F3;
    }
    .ic {
      margin-right: 10px;
    }
  }
}
