.b-card {
  margin-bottom: 16px;
  padding: 16px;
  border-radius: 6px;
  background: var(--white, #FFF);
  &__title {
    margin-bottom: 2px;
    color: var(--grays-500, #6C7685);
    font-size: 12px;
    line-height: 125%;
    letter-spacing: 0.24px;
    text-transform: uppercase;
  }
  &__text {
    margin-bottom: 12px;
    color: var(--grays-900, #1C2229);
    font-size: 16px;
    font-weight: 500;
    line-height: 150%;
  }
  &__group {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
  }
  &__number {
    margin-right: 10px;
    color: var(--grays-1000, #0E1115);
    font-size: 22px;
    font-weight: 700;
    line-height: 120%;
  }
  &__label {
    display: inline-flex;
    align-items: center;
    font-size: 14px;
    font-weight: 500;
    line-height: 125%;
  }
}

.b-card-2-group {
  display: flex;
  flex-wrap: wrap;
}

.b-card-2 {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 320px;
  margin-right: 16px;
  margin-bottom: 16px;
  padding: 16px;
  border-radius: 6px;
  border: 1px solid var(--grays-100, #EDEEF0);
  background: var(--white, #FFF);
  transition: all .3s;
  &:hover {
    text-decoration: none;
    border-color: var(--main-green-600, #2F8733);
    box-shadow: 0px 0px 6px 0px rgba(22, 126, 33, 0.25);
  }
  &__title {
    min-height: 31px;
    margin-right: 12px;
    color: var(--grays-600, #475467);
    font-size: 13px;
    font-weight: 500;
    line-height: 120%;
    letter-spacing: 0.13px;
    text-transform: uppercase;
  }
  &__group {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    + .b-card-2__group {
      margin-top: 8px;
    }

    .ic {
      width: 36px;
      height: 36px;
      &:not(.ic-smile) {
        fill: #9198A4;
      }
    }
  }
  &__number {
    margin-right: 12px;
    color: var(--grays-1000, #0E1115);
    font-size: 28px;
    font-weight: 700;
    line-height: 128%;
  }
  &__number-note {
    display: inline-block;
    margin-left: 8px;
    font-size: 16px;
    font-weight: normal;
    color: #475467;
  }
  &__label {
    display: inline-flex;
    align-items: center;
    font-size: 14px;
    font-weight: 500;
    line-height: 125%;
    white-space: nowrap;
  }


  @include media-breakpoint-up(md) {
    &_lg {
      width: 432px;
    }
  }


  @media (max-width: 480px) {
    width: 100%;
    margin-right: 0;
  }
}

.b-card-3-group {
  display: flex;
  flex-wrap: wrap;
}

.b-card-3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 440px;
  max-width: 33.33%;
  flex: 0 0 auto;
  margin-right: 4px;
  margin-bottom: 4px;
  padding: 12px 16px;
  border-radius: 7px;
  border: 1px solid var(--grays-100, #EDEEF0);
  color: var(--grays-800, #2B323E);
  font-size: 14px;
  line-height: 140%;

  &__main {
    flex-grow: 1;
  }
  &__title {
    font-weight: 500;
  }
  &__btn-group {
    display: flex;
    justify-content: space-between;
    width: 100%;
    .btn {
      flex: 0 0 auto;
      width: calc(50% - 6px);
    }
  }

  &_no-bdr {
    border: none;
  }


  @include media-breakpoint-down(md) {
    max-width: calc(50% - 4px);
  }
  @include media-breakpoint-down(sm) {
    max-width: 100%;
    margin-right: 0;
  }
}

.b-card-4 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  border-radius: 6px;
  border: 1px solid var(--Grays-200, #DADDE1);
  &__group {
    &:last-child {
      padding-left: 8px;
    }
  }
  &__info {
    padding: 0 4px;
    color: var(--Grays-500, #6C7685);
    font-size: 11px;
  }
  &__title {
    padding: 2px 4px;
    color: var(--Grays-900, #1C2229);
  }
  &__btn {
    padding: 8px;
    border: none;
    background-color: transparent;
    .ic {
      width: 20px;
      height: 20px;
      stroke: #9198A4;
    }
  }
}

.b-card-5 {
  color: #1C2229;
  border-width: 1px;
  border-style: solid;
  &__area {
    display: flex;
    align-items: center;
    width: 132px;
    padding: 16px;
    justify-content: center;
    background-color: #F0F7FE;
    border-radius: 4px;
    .btn {
      font-size: 9px;
    }
  }
  .h7 {
    line-height: 1.25;
  }
  .h8 {
    line-height: 1.4;
  }
  &:hover {
    text-decoration: none;
    border-color: #2F8733;
  }

  &[aria-expanded="true"],
  .active {
    border-color: #2F8733;
  }
}
