.b-page {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  background-color: #F5F7FA;
  background-size: cover;
  background-position: 50%;

  &_bg-mod-a {
    background-color: #EDEEF0;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    padding: 16px;
  }
  &__header-group {
    display: flex;
  }
  &__header-btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    margin-left: 8px;
    background-color: #fff;
    border: 1px solid #DADDE1;
    border-radius: 7px;
    .ic {
      width: 24px;
      height: 24px;
      stroke: #1C2229;
    }
  }
  &__header-language {
    .ui-language {
      .ui-select {
        padding-top: 9px;
        padding-bottom: 9px;
        background-color: #FFF;
        font-size: 0;
      }
      .dropdown-toggle .ic {
        margin-right: 0;
      }
    }
  }
  &__header-counter {
    position: absolute;
    left: -4px;
    bottom: -4px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    padding: 2px;
    border-radius: 50%;
    background-color: #D82518;
    color: #fff;
    font-size: 13px;
    font-weight: 600;
  }
  &__logo {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 64px;
    height: 64px;
    padding: 4px;
    background-color: #FFF;
    overflow: hidden;
    border: 1px solid #DADDE1;
    border-radius: 8px;
  }
  &__body {
    display: flex;
    align-items: center;
    flex-grow: 1;
  }
  &__inner {
    width: 100%;
  }


  &_w-bg {
    background-position: 50% 0;
    background-size: 100% auto;
    color: #FFF;

    .b-menu-list__link {
      text-shadow: 0 2px 2px rgba(0,0,0,.25);
      min-height: 128px;
      color: #FFF;
      background-blend-mode: overlay;
      background-color: rgba(0,0,0,.4);
      background-position: 50%;
      background-size: cover;
    }
    .b-menu__title {
      background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 100%);
    }
  }
}

.b-page-card {
  max-width: calc(100vw - 40px);
  margin: auto;
  box-shadow: 0 24px 48px -12px rgba(0, 0, 0, 0.18);
  border-radius: 24px;
  background-color: #FFF;

  &__main {
    padding: 24px;
    text-align: center;
  }
  &__logo {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 64px;
    height: 64px;
    margin-bottom: 12px;
    padding: 4px;
    background-color: #FFF;
    overflow: hidden;
    border: 1px solid #EDEEF0;
    border-radius: 50%;
  }
  &__title {
    margin-bottom: 8px;
    font-size: 24px;
    font-weight: 700;
  }
  &__info {
    font-size: 16px;
    color: #6C7685;
  }
  &__footer {
    padding: 4px 0;
    border-top: 1px solid #EDEEF0;
  }
  &__footer-item {
    display: flex;
    padding: 16px;
  }
  &__foot-ic {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 0 0 auto;
    width: 40px;
    height: 40px;
    margin-right: 16px;
    padding: 0;
    border-radius: 50%;
    background-color: #2F8733;
    border: none;
    text-decoration: none;
    .ic {
      stroke: #fff;
    }
  }
  &__foot-title {
    display: block;
    font-weight: 500;
    font-size: 16px;
    color: #1C2229;
  }
  &__foot-info {
    display: block;
    color: #6C7685;
  }
  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 150px;
    border: 1px solid #EDEEF0;
    border-radius: 11px;
    background: #F9FAFB;
    overflow: hidden;
    &_sm {
      width: 220px;
      margin-left: auto;
      margin-right: auto;
    }
  }
  &__video {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #EDEEF0;
    border-radius: 11px;
    background: #F9FAFB;
    overflow: hidden;
    &_h220 {
      height: 220px;
    }
  }

  &_w420 {
    width: 100%;
  }


  @media (min-width: 520px) {
    &_w420 {
      width: 420px;
    }
  }
}
