.b-main {
  display: flex;
  width: calc(100% - 64px);
  flex-grow: 1;
  &__sort {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    .ui-select-group__item {
      margin-bottom: 8px;
    }
  }
  &__title {
    font-weight: 500;
    font-size: 18px;
    line-height: 140%;
    color: #1C2229;
  }
  &__content {
    flex-grow: 1;
    padding: 16px 24px;
    height: calc(100vh - 68px);
  }
  &__inner {
    flex-grow: 1;
  }
  > .tab-pane.active {
    display: flex;
    width: 100%;
  }

  @include media-breakpoint-down(md) {
    width: 100%;
    &__content {
      height: calc(100vh - 175px);
    }
    &__inner {
      width: 100%;
    }
  }

  @include media-breakpoint-down(sm) {
    &__content {
      padding-left: 12px;
      padding-right: 12px;
    }
  }
}

.b-main-nav {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 0 0 auto;
  width: 280px;
  height: 100%;
  background-color: #FAFAFA;
  border-right: 1px solid #EDEEF0;
  &__inner {
    flex-grow: 1;
  }
  &__header {
    height: 60px;
    padding: 18px 16px;
  }
  &__main {
    padding: 8px;
  }
  &__footer {
    padding: 16px 12px;
    border-top: 1px solid #EDEEF0;
  }
  &__list {
    padding-left: 0;
    list-style-type: none;
    &.show {
      display: block;
    }
    .b-main-nav__list {
      display: none;
      margin-left: 10px;
      &.show {
        display: block;
      }
    }
  }
  &__item {
    position: relative;

    &.active {
      > .b-main-nav__link {
        background: #EDEEF0;
        color: #2F8733;
        &:after {
          border-color: transparent #2F8733 transparent  transparent;
        }
      }
    }
    .ui-dropdown-more {
      position: absolute;
      top: 2px;
      right: 2px;
      opacity: 0;
      transition: all .3s;
    }
  }
  &__trigger {
    position: absolute;
    top: 2px;
    left: 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 4px;
    border: none;
    background-color: transparent;
    border-radius: 6px;
    z-index: 1;
    transition: all .2s;
    .ic {
      transform: rotate(-90deg);
      transition: all .1s;
    }

    &.open {
      .ic {
        transform: rotate(0);
      }
    }
    &:hover {
      background-color: #EAF3EB;
    }
  }
  &__link {
    display: flex;
    justify-content: space-between;
    margin-bottom: 4px;
    padding: 7.5px 12px 7.5px 42px;
    font-size: 15px;
    line-height: 140%;
    color: #394352;
    border-radius: 6px;
    cursor: default;
    transition: all .2s;
    &:hover {
      text-decoration: none;
      background-color: #F3F7F3;
      .b-main-nav__number {
        opacity: 0;
      }
      .ui-dropdown-more {
        opacity: 1;
      }
    }
  }
  a.b-main-nav__link {
    cursor: pointer;
    .b-main-nav__number {
      opacity: 1;
    }
  }
  &__number {
    font-weight: 500;
    transition: all .3s;
  }

  @include media-breakpoint-down(md) {
    display: none;
    &__item {
      .ui-dropdown-more {
        position: relative;
        top: auto;
        right: auto;
        opacity: 1;
      }
    }
    &__name {
      flex-grow: 1;
    }
    &__link {
      align-items: center;
      padding-top: 2px;
      padding-right: 2px;
      padding-bottom: 2px;
      &:hover {
        .b-main-nav__number {
          opacity: 1;
        }
      }
    }
    &__number {
      width: 32px;
      margin-right: 6px;
      text-align: center;
    }
  }
}


.b-main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: calc(100vw - 64px);
  height: 60px;
  padding: 12px 24px;
  border-bottom: 1px solid #EDEEF0;
  background-color: #FFF;

  &_template {
    height: 68px;
  }


  &__main {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    height: 100%;
  }

  &__nav {
    margin-right: 50px;
    display: flex;
    flex-grow: 1;
  }
  &__ic {
    position: relative;
    top: -2px;
    width: 16px;
    height: 16px;
    margin-right: 5px;
    border-radius: 50%;
    display: inline-flex;
    flex: 0 0 auto;
    align-items: center;
    justify-content: center;
    border: 1.25px solid #475467;
    .ic {
      stroke: #475467;
    }
  }
  &__survey {
    background-color: #FFF;
  }
  &__item {
    display: inline-flex;
    font-weight: 500;
    line-height: 1;
    color: #1C2229;
    &:not(:last-child) {
      margin-right: 28px;
    }
    &:hover {
      text-decoration: none;
    }
    &.st-active {
      color: #2F8733;
      cursor: default;
      pointer-events: none;
      .b-main-header__ic {
        border-color: #2F8733;
        .ic {
          stroke: #2F8733;
        }
      }
    }
    &.st-prev {
      color: #1C2229;
      .b-main-header__ic {
        background-color: #16B364;
        border-color: #16B364;
        .ic {
          stroke: #fff;
        }
      }
    }
    &.st-disabled {
      opacity: 0.3;
      cursor: default;
      pointer-events: none;
      .b-main-header__ic {
        border-color: #475467;
        .ic {
          stroke: #475467;
        }
      }
    }
  }

  &_qr {
    background-color: #FAFAFA;

    .breadcrumb-wrap {
      background-color: transparent;
    }
  }

  &_template {
    .breadcrumb-wrap {
      width: 346px;
      .breadcrumb {
        white-space: normal;
      }
    }
  }

  @include media-breakpoint-only(lg) {
    &_template {
      .breadcrumb-wrap {
        width: 270px;
      }
    }
  }

  @include media-breakpoint-down(lg) {
    &_template {
      .breadcrumb-wrap {
        min-width: 0;
      }
    }
  }

  @media (max-width: 1199.98px) {
    &__ic {
      display: none;
    }
  }

  @include media-breakpoint-down(md) {
    flex-wrap: wrap;
    width: 100%;
    height: auto;
    padding: 18px 24px 13px;
    &__nav {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-grow: 1;
      margin-right: 0;
      margin-bottom: 14px;
      padding-left: 20px;
      padding-right: 20px;
      padding-bottom: 15px;
      border-bottom: 1px solid #EDEEF0;
      white-space: nowrap;
      overflow-x: auto;
      overflow-y: hidden;
    }
    &__item {
      &:not(:last-child) {
        margin-right: 0;
        padding-right: 20px;
      }
    }
    .btn-wrap {
      background-color: #FFF;
    }
    .breadcrumb-wrap {
      background-color: #FFF;
    }
    &__btn {
      order: 2;
    }

    &_template {
      &__nav {
        order: 0;
      }
      &__btn {
        order: 2;
      }
      .breadcrumb-wrap {
        order: 1;
      }
      .breadcrumb {
        padding-right: 0;
      }
    }

    &_design {
      .b-main-header{
        &__btn {
          display: none;
        }
        &__nav {
          margin-bottom: 0;
          padding-bottom: 0;
          border-bottom: none;
        }
      }
      .breadcrumb-wrap {
        display: none;
      }
    }
  }


  @include media-breakpoint-down(sm) {
    padding-left: 12px;
    padding-right: 12px;

    &__nav {
      display: block;
      padding-left: 0;
    }

    &_template {
      .breadcrumb-wrap {
        width: calc(100% - 130px);
      }
    }

    // &_structure {
    //   padding-left: 0;
    //   padding-right: 0;
    //   .breadcrumb-wrap {
    //     padding-left: 12px;
    //     padding-right: 12px;
    //   }
    //   .btn-wrap {
    //     padding-left: 12px;
    //     padding-right: 12px;
    //   }
    // }
  }
}

.b-main-sublocation {
  padding-top: 22px;
  &__title {
    margin-bottom: 12px;
    font-weight: 500;
    line-height: 140%;
    color: #2B323E;
  }
  &__group {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  &__item {
    display: flex;
    align-items: center;
    min-width: 250px;
    margin-right: 16px;
    margin-bottom: 16px;
    padding: 6px 6px 6px 16px;
    border: 1px solid #6C7685;
    border-radius: 6px;
    background-color: #FFF;
    cursor: default;
    transition: all .3s;
    .ui-dropdown-more {
      margin-left: 16px;
    }
    &:hover {
      background: #F3F7F3;
      border-color: #2F8733;
    }
  }
  &__name {
    flex-grow: 1;
    margin-left: 16px;
    color: #1C2229;
  }
}
