.b-partner-group {
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
}

.b-partner {
  width: 226px;
  margin-right: 12px;
  font-size: 12px;
  text-align: center;
  &__main {
    padding: 12px;
    border-radius: 6px;
    background-color: #EDEEF0;
  }
  &__status {
    display: block;
    margin-bottom: 2px;
    font-weight: 500;
    color: #2B323E;
  }
  &__title {
    margin-bottom: 2px;
    font-size: 14px;
    font-weight: 700;
    color: #394352;
  }
  &__text {
    margin-bottom: 12px;
  }
  &__price {
    margin-top: 16px;
    margin-bottom: 16px;
    padding: 12px 8px;
    background-color: rgba(0, 0, 0, 0.05);
    border-radius: 6px;
    color: #2F8733;
    .progress {
      height: 4px;
      background-color: #F5F7FA;
    }
  }
  &__price-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 4px;
    font-weight: 600;
  }
  &__price-total {
    font-weight: 400;
  }
  &__price-percent {
    font-weight: 500;
  }
  &__info {
    color: #1C2229;
  }
  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 8px;
    color: #2B323E;
    .ic {
      margin-left: 6px;
      transform: rotate(-90deg);
    }
  }
  &__figure {
    margin-bottom: 12px;
  }

  &.st-active & {
    &__main {
      background-color: #F1E1A7;
      border: 2px solid #2F8733;
      box-shadow: 0 0 0 2px rgba(47, 135, 51, 0.3);
    }
  }
}
