.b-frame {
  background: #FFFFFF;
  border: 1px solid #DADDE1;
  border-radius: 10px;

  &.mob-preview {
    flex: 0 0 auto;
    width: 383px;
    margin-left: auto;
    margin-right: auto;
    .b-frame__input-wrap,
    .b-frame__area-wrap,
    .b-frame__input,
    .b-frame__content-wrap {
      width: 100%;
    }
    .b-frame__content-inner {
      padding-left: 0;
      padding-right: 0;
    }
    .b-frame__content {
      padding-left: 14px;
      padding-right: 14px;
    }

    &.b-frame_widget-support {
      background: url(../img/content/b-website/bg-mob.svg);
      background-size: cover;
    }

    .b-page-card_w420 {
      width: 100%;
    }
  }

  + {
    .b-frame {
      margin-top: 75px;
    }
  }
  &__header {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 4px 20px;
    border-bottom: 1px solid #EDEEF0;

    &:before {
      content: '';
      display: block;
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background: #D9D9D9;
      box-shadow: 22px 0 0  #D9D9D9,  44px 0 0  #D9D9D9;
    }

    &_not-dots {
      &:before {
        content: none;
      }
    }
  }
  & &__header {
    .ui-language {
      width: auto;
    }
  }
  &__body {
    padding: 0 24px;
  }
  &__img-site {
    width: 100%;
    height: auto;
  }
  &__item {
    margin-bottom: 32px;
    border: 1px solid #EDEEF0;
    border-radius: 8px;
    &:last-child {
      margin-bottom: 24px;
    }
  }
  &__option-item {
    margin-bottom: 10px;
    &.st-disabled {
      opacity: 0.4;
    }
    &.st-default {
      .b-frame__option-input {
        opacity: 0.3;
      }
    }
  }
  &__option-select {
    position: relative;
    display: inline-grid;
    width: auto;
    align-items: center;
    color: var(--grays-900, #1C2229);
    font-size: 16px;
    line-height: 140%;

    &:not(.ui-radio-abs__label) {
      padding-left: 36px;
    }

    &::after {
      content: attr(data-value) ' ';
      visibility: hidden;
      white-space: pre-wrap;
      width: auto;
      min-width: 1em;
      grid-area: 1/2;
    }
    .ic {
      position: absolute;
      top: 9px;
      left: 8px;
      width: 18px;
      height: 18px;
      stroke: #6C7685;
    }

    &_multi {
      .ic {
        stroke: var(--grays-900, #1C2229);
      }
    }
  }
  &__option-multi {
    .b-frame__option-item:not(.st-disabled) {
      &:nth-child(1) {
        .b-frame__option-select_multi {
          border: 1px solid #D5BFBE;
          background: #FCEDEC;
          color: #762926;
          border-radius: 5px;
          .ic {
            stroke: #762926;
          }
        }
      }
      &:nth-child(2) {
        .b-frame__option-select_multi {
          border: 1px solid #D5C5B8;
          background: #FDF1E7;
          color: #773D13;
          .ic {
            stroke: #773D13;
          }
        }
      }
      &:nth-child(3) {
        .b-frame__option-select_multi {
          border: 1px solid #DAD7C5;
          background: #FCF1AD;
          color: #7E7232;
          .ic {
            stroke: #7E7232;
          }
        }
      }
      &:nth-child(4) {
        .b-frame__option-select_multi {
          border: 1px solid #C1D0BE;
          background: #ECF9EC;
          color: #2E6325;
          .ic {
            stroke: #2E6325;
          }
        }
      }
      &:nth-child(5) {
        .b-frame__option-select_multi {
          border: 1px solid #BAC5D8;
          background: #E8F2FE;
          color: #163C7C;
          .ic {
            stroke: #163C7C;
          }
        }
      }
      &:nth-child(6) {
        .b-frame__option-select_multi {
          border: 1px solid #C7BDD8;
          background: #F2EDFE;
          color: #41257B;
          .ic {
            stroke: #41257B;
          }
        }
      }
      &:nth-child(7) {
        .b-frame__option-select_multi {
          border: 1px solid #DFCDDF;
          background: #FAE9FB;
          color: #711B6F;
          .ic {
            stroke: #711B6F;
          }
        }
      }
      &:nth-child(8) {
        .b-frame__option-select_multi {
          border: 1px solid #DDDDDC;
          background: #F1F1F1;
          color: #373530;
          .ic {
            stroke: #373530;
          }
        }
      }
    }
  }
  &__option-input {
    border: none;
    padding: 0;
    width: auto;
    min-width: 1em;
    grid-area: 1/2;
    color: inherit;
    background-color: transparent;
    font-size: 16px;
    &:focus {
      outline: none;
      box-shadow: none;
    }
  }
  &__input {
    width: 330px;
  }
  &__input-group {
    position: relative;
    .ic {
      position: absolute;
      left: 8px;
      top: 9px;
      stroke: #9198A4;
    }
    .b-frame__input {
      padding-left: 36px;
    }
  }
  &__signature {
    max-width: 420px;
    padding: 20px 24px;
    border-radius: 8px;
    border: 1px dashed var(--grays-300, #B5BBC2);
    background: var(--White, #FFF);
    text-align: center;
  }
  &__signature-main {
    position: relative;
    height: 57px;
    margin-bottom: 13px;
    &:before {
      content: '';
      position: absolute;
      bottom: -5px;
      left: 12px;
      right: 12px;
      height: 1px;
      background: var(--grays-100, #EDEEF0);
    }
  }
  &__signature-text {
    padding: 2px 4px;
    color: var(--grays-400, #9198A4);
    font-size: 12px;
    line-height: 125%;
    border: none;
    background-color: #FFF;
    text-align: center;
  }
  &__header-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4px 12px;
    font-size: 15px;
    line-height: 140%;
    color: #1C2229;
    border-bottom: 1px solid #EDEEF0;

    .dropdown-menu_mod-a {
      width: 300px;
      max-height: calc(100vh - 150px);
    }
  }
  &__header-select {
    .ic {
      margin-right: -6px;
      margin-left: -8px;
      stroke: #475467;
    }
  }
  &__header-note {
    color: #D92D20;
  }
  &__content {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    line-height: 140%;
  }
  &__content-inner {
    width: 100%;
    padding: 58px 14px 8px;
  }
  &__content-wrap {
    width: 586px;
    margin-left: auto;
    margin-right: auto;
    text-align: left;
    .label-note {
      position: absolute;
      top: 0;
      right: 0;
    }
  }
  &__title {
    width: 100%;
    margin-bottom: 6px;
    font-size: 18px;
    line-height: 1.4;
    text-align: center;
    &_lg {
      font-size: 22px;
      font-weight: 500;
    }
  }
  &__title-wrap {
    position: relative;
    max-width: 580px;
    margin-left: auto;
    margin-right: auto;
    &_lg {
      min-height: 98px;
      display: flex;
      align-items: center;
    }
  }
  &__lg-title {
    width: 100%;
    font-size: 28px;
    font-weight: 700;
    line-height: 1;
  }
  &__subtitle {
    width: 100%;
    margin-bottom: 6px;
    font-size: 13px;
    line-height: 1.4;
    color: inherit;
    background-color: inherit;
    text-align: center;
  }
  &__desrip-wrap {
    position: relative;
    min-height: 73px;
  }
  &__desrip {
    width: 100%;
    margin-top: 20px;
    margin-bottom: 80px;
    font-size: 16px;
    text-align: center;

    &_sm {
      font-size: 14px;
    }
  }
  &__input-wrap {
    width: 420px;
    margin-top: 18px;
    margin-left: auto;
    margin-right: auto;
    .form-control {
      height: 44px;
    }
  }
  &__area-wrap {
    position: relative;
    display: inline-flex;
    width: 420px;
    margin: 28px 0;
    border: 1px solid #D0D3D8;
    border-radius: 4px;
  }

  & &__area {
    width: 100%;
    height: 115px;
    padding: 12px 8px;
    resize: none;
    font-size: 15px;
    &_w-btn {
      padding-bottom: 32px;
    }
  }
  &__area-ics {
    position: absolute;
    bottom: 0;
    left: 0;
    display: inline-flex;

    .dropdown-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .ic {
        width: 20px;
        height: 20px;
      }
    }
  }

  &__area-ic {
    display: none;
    align-items: center;
    justify-content: center;
    width: 36px;
    height: 36px;
    margin: 2px;
    padding: 0;
    border: none;
    background-color: transparent;
    fill: #B5BBC2;
    &.active {
      display: inline-flex;
    }
    &.st-recorded {
      fill: #0359FF;
    }
  }
  &__title-note {
    color: #E83137;
  }
  &__footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 28px;
    padding-bottom: 16px;
    .btn {
      padding: 10px 20px;
      background: #0359FF;
      border-radius: 6px;
      font-weight: 500;
      font-size: 16px;
      line-height: 140%;
      color: #FFFFFF;
    }
  }
  &__copyright {
    display: flex;
    align-items: center;
    justify-content: center;
    width: calc(100% - 2px);
    padding: 16px;
    color: var(--grays-800, #2B323E);
    font-size: 13px;
    font-weight: 500;
    line-height: 120%;
    background: var(--dark-50, #FAFAFA);
    border-radius: 0 0 10px 10px;
    a {
      display: inline-flex;
      color: var(--grays-800, #2B323E);
    }

    .ic {
      width: 16px;
      height: 16px;
      margin-left: 6px;
      margin-right: 6px;
      flex: 0 0 auto;
    }
  }
  &__logo {
    width: 96px;
    margin-top: 12px;
  }
  &__qr-img {
    width: 100%;
    margin-bottom: 8px;
    text-align: center;
    img {
      max-width: 100%;
      height: auto;
      margin-left: auto;
      margin-right: auto;
    }
  }
  &__logo-dev {
    display: block;
    width: 100%;
    margin-top: 36px;
    padding-bottom: 20px;
    text-align: center;
    img {
      width: 156px;
      height: auto;
    }
  }

  &__empty-1 {
    min-height: 88px;
  }
  &__empty-2 {
    min-height: 267px;
  }
  &__empty-3 {
    min-height: 200px;
  }
  &__empty-4 {
    min-height: 232px;
  }


  &_qr {
    max-width: 525px;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    .b-frame {
      &__header {
        &:before {
          content: none;
        }
      }
      &__body {
        // width: 523px;
      }
      &__content {
        display: block;
      }
      &__title-wrap {
        max-width: 312px;
        margin-left: auto;
        margin-right: auto;
      }
    }
    .b-frame-logo {
      padding-top: 94px;
      margin-bottom: 25px;
    }
    .b-frame-icons {
      margin-top: 14px;
      margin-bottom: 64px;
    }
  }


  &.st-landscape {
    width: 747px;
    max-width: 100%;
    .b-frame {
      &__body-wrap {
        padding-left: 8px;
      }
      &__content {
        display: flex;
        justify-content: space-between;
        padding-top: 113px;
        padding-bottom: 20px;
      }
      &__qr-top {
        flex: 0 0 auto;
        width: 47%;
        padding-bottom: 24px;
      }
      &__qr-bottom {
        flex: 0 0 auto;
        width: 46%;
      }
      &__title {
        margin-bottom: 3px;
      }
      &__logo-dev {
        padding-bottom: 15px;
        margin-top: 42px;
      }
    }
    .b-frame-logo {
      padding-top: 7px;
    }
    .b-frame-icons {
      margin-bottom: 0;
    }
  }

  &_website {
    position: relative;
    overflow: hidden;
    // max-height: calc(100vh - 165px);
    height: 100%;
    .bs-messenger {
      position: absolute;

      &__body {
        max-height: calc(100vh - 410px);
      }

      &[data-position="left-top"] {
        top: 36px;
        left: 24px;
        right: auto;
        bottom: auto;

        .bs-messenger {
          &__dropdown {
            left: 0;
            right: auto;
            bottom: auto;
            top: 68px;
          }
        }
      }

      &[data-position="left-center"] {
        left: 0;
        top: 50%;
        bottom: auto;
        transform: translateY(-50%);

        .bs-messenger {
          &__dropdown {
            left: 64px;
            right: auto;
            bottom: auto;
            top: 0;
            transform: translateY(-50%);
          }
        }
      }

      &[data-position="left-bottom"] {
        left: 24px;
        bottom: 36px;

        .bs-messenger {
          &__dropdown {
            left: 0;
            right: auto;
          }
        }
      }

      &[data-position="right-top"] {
        top: 36px;
        right: 24px;
        bottom: auto;

        .bs-messenger {
          &__dropdown {
            bottom: auto;
            top: 68px;
          }
        }
      }

      &[data-position="right-center"] {
        right: 0;
        top: 50%;
        bottom: auto;
        transform: translateY(-50%);

        .bs-messenger {
          &__dropdown {
            right: 64px;
            bottom: auto;
            top: 0;
            transform: translateY(-50%);
          }
        }
      }

      &[data-position="right-bottom"] {
        bottom: 36px;
        right: 24px;

        .bs-messenger {
          &__dropdown {
          }
        }
      }
    }
  }


  @include media-breakpoint-down(lg) {
    &.st-landscape {
      width: auto;
    }
    &__input-wrap,
    &__area-wrap {
      width: 400px;
    }
    &__content-wrap {
      width: 100%;
      padding-left: 24px;
      padding-right: 24px;
    }
    &__title-wrap {
      max-width: 100%;
    }
  }


  @include media-breakpoint-up(md) {
    &_widget-support {
      background: url(../img/content/b-website/bg.svg);
      background-size: cover;
    }
  }

  @media screen and (max-width:1439px) {
    &_qr {
      &.st-landscape {
        .b-frame {
          &__body {
            overflow-x: auto;
          }
          &__body-wrap {
            min-width: 730px;
          }
        }
      }
    }
  }

  @media screen and (max-width:1199px) {
    &__input-wrap,
    &__area-wrap {
      width: 100%;
    }
  }

  @include media-breakpoint-down(md) {
    &_widget-support {
      background: url(../img/content/b-website/bg-mob.svg);
      background-size: cover;
    }
    &__lg-title {
      font-size: 22px;
    }
    &_website {
      height: calc(100vh - 282px);
      .bs-messenger {
        &__body {
          max-height: calc(100vh - 530px);
        }
      }
    }
  }


  @include media-breakpoint-down(sm) {
    &__area-wrap {
      width: calc(100% - 28px);
      margin-left: 14px;
      margin-right: 14px;
    }
    &__input,
    &__input-wrap {
      width: 100%;
    }
    &__lg-title {
      font-size: 18px;
    }
    &_website {
      max-height: calc(100vh - 280px);
    }

    &__content-inner {
      padding-left: 0;
      padding-right: 0;
    }


    &_qr {
      width: 100%;

      &.st-landscape {
        .b-frame {
          &__body {
            overflow-x: auto;
          }
          &__body-wrap {
            width: 730px;
          }
        }
      }
    }
  }

  @media screen and (max-width:390px) {
    &_website {
      .bs-messenger {
        &__dropdown {
          width: 260px;
        }
      }
    }
  }
}

.b-frame-icons {
  position: relative;
  display: inline-flex;
  flex-wrap: wrap;

  &__labels {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translate(-50%);
    display: flex;
    align-items: center;
    justify-content: space-between;
    white-space: nowrap;
    width: 100%;
    min-width: 220px;
    padding-right: 12px;
    font-size: 12px;
    line-height: 125%;
    color: #6C7685;
  }
  &__label {
    &:first-child {
      margin-right: 6px;
      text-align: left;
    }
    &:last-child {
      margin-left: 6px;
      text-align: right;
    }
  }

  &__btn {
    padding: 12px 0 10px;
    border-radius: 8px;
    border: none;
    background-color: #FFF;
    &:after {
      border-top-color: #B5BBC2;
    }

  }
  &__list,
  &__list-modal {
    max-width: 100%;
    display: inline-flex;
    list-style-type: none;
    padding: 0;
    margin: 0 -6px;
    counter-reset: item;

    &.st-type-star {
      fill: #FDCA47;
    }
    &.st-type-heart {
      fill: #DF2540;
    }
    &.st-type-like {
      .b-frame-icons__item {
        &:first-child {
          fill: #AE241A;
          border-color: #AE241A;
          .ic {
            margin-top: 5px;
            transform: scale(1, -1);
          }
        }
      }
    }

    &[data-selected-icons="default"] {
      .b-frame-icons {
        &__item {
          .ic {
            fill: #B5BBC2;
          }
          &_like {
            border-color: #B5BBC2;
          }
        }
      }
    }
    &[data-selected-icons="1"] {
      .b-frame-icons {
        &__item {
          &:nth-child(n+2) {
            .ic {
              fill: #B5BBC2;
            }
          }
          &_like {
            &:first-child {
              fill: #AE241A;
              border-color: #AE241A;
            }
            &:last-child {
              border-color: #B5BBC2;
            }
          }
        }
      }
    }
    &[data-selected-icons="2"] {
      .b-frame-icons {
        &__item {
          &:nth-child(n+3) {
            .ic {
              fill: #B5BBC2;
            }
          }
          &_like {
            &:first-child {
              fill: #B5BBC2;
              border-color: #B5BBC2;
            }
            &:last-child {
              fill: #128F50;
              border-color: #128F50;
            }
          }
        }
      }
    }
    &[data-selected-icons="3"] {
      .b-frame-icons {
        &__item {
          &:nth-child(n+4) {
            .ic {
              fill: #B5BBC2;
            }
          }
        }
      }
    }
    &[data-selected-icons="4"] {
      .b-frame-icons {
        &__item {
          &:nth-child(n+5) {
            .ic {
              fill: #B5BBC2;
            }
          }
        }
      }
    }
    &[data-selected-icons="5"] {
      .b-frame-icons {
        &__item {
          &:nth-child(n+6) {
            .ic {
              fill: #B5BBC2;
            }
          }
        }
      }
    }
    &[data-selected-icons="6"] {
      .b-frame-icons {
        &__item {
          &:nth-child(n+7) {
            .ic {
              fill: #B5BBC2;
            }
          }
        }
      }
    }
    &[data-selected-icons="7"] {
      .b-frame-icons {
        &__item {
          &:nth-child(n+8) {
            .ic {
              fill: #B5BBC2;
            }
          }
        }
      }
    }
    &[data-selected-icons="8"] {
      .b-frame-icons {
        &__item {
          &:nth-child(n+9) {
            .ic {
              fill: #B5BBC2;
            }
          }
        }
      }
    }
    &[data-selected-icons="9"] {
      .b-frame-icons {
        &__item {
          &:nth-child(n+10) {
            .ic {
              fill: #B5BBC2;
            }
          }
        }
      }
    }
  }
  &__item {
    padding-left: 6px;
    padding-right: 6px;
    counter-increment: item;

    input[type="radio"] {
      width: 0;
      height: 0;
      opacity: 0;
      position: absolute;
    }

    img,
    .ic {
      max-width: 100%;
      height: auto;
    }

    &_like {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 80px;
      height: 80px;
      margin-left: 4px;
      margin-right: 4px;
      border: 1px solid #AE241A;
      border-radius: 4px;
      &:last-child {
        border-color: #128F50;
      }
      label {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
      }
    }
    &_count {
      position: relative;
      font-weight: 500;
      font-size: 14px;
      color: #fff;
      fill: #D2483F;
      &.st-neutral {
        fill: #F4953E;
      }
      &.st-positive {
        fill: #16B364;
      }

      label {
        position: relative;
      }

      span {
        position: absolute;
        top: 53%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
  &__collapse {
