.grid {
  display: grid;
}

.l-page {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  min-height: 100vh;
  padding-left: 64px;
  overflow: hidden;
  transition: all .3s;

  &.fixed-nav {
    padding-left: 280px;

    .b-main {
      width: calc(100% - 280px);
      overflow-x: auto;
    }
  }

  @include media-breakpoint-down(md) {
    align-content: flex-start;
    padding-left: 0;
    overflow: visible;
  }
}

.modal-md {
    max-width: 620px;
}
.btn-white {
