.btn-w-ic {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  .ic {
    transition: all .15s;
    stroke: inherit;
    &:first-child {
      margin-right: 8px;
    }
    &:last-child {
      margin-left: 8px;
    }
  }
}

.btn-w-left-ic {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding-left: 10px;
  .ic {
    stroke: inherit;
    margin-right: 8px;
  }
}

.btn-w-right-ic {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding-right: 10px;
  .ic {
    stroke: inherit;
    margin-left: 8px;
  }
}

.btn-passw {
  position: absolute;
  top: 6px;
  right: 10px;
  display: flex;
  padding: 0;
  border: none;
  background-color: transparent;

  .ic-view {
    display: none;
  }

  .ic {
    stroke: #686A6E;
  }

  &.st-view {
    .ic:not(.ic-view) {
      display: none;
    }
    .ic-view {
      display: block;
    }
  }
}

.btn-xl {
  font-size: 16px;
  padding: 15px 24px;
  line-height: 1;
}

.btn-round {
