  }
}

.ui-dropdown-more {
  width: 32px;
  height: 32px;
  border-radius: 6px;
  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: 0;
    border: none;
    background-color: transparent;
    border-radius: 6px;
    transition: all .2s;

    .ic {
      fill: #394352;
    }

    &:after {
      content: none;
    }
    &:hover {
      background-color: #EAF3EB;
    }
  }
}

.form-group {
  &_passw {
