.label-bold {
  font-weight: 500;
}
.w-md-auto {
  @include media-breakpoint-up(sm) {
    width: auto !important;
  }
}
.bootstrap-select .inner {
  scrollbar-color: #D0D5DD transparent;
  scrollbar-width: thin;
  &::-webkit-scrollbar {
    -webkit-appearance: none;
  }

  &::-webkit-scrollbar:vertical {
    width: 4px;
  }

  &::-webkit-scrollbar-thumb {
    background: #D0D5DD;
    border-radius: 2px;
  }
}

.ui-date-picker-wrap {
  position: relative;
  display: flex;
  background: #F5F7FA;
  border: 1px solid #EDEEF0;
  border-radius: 4px;
}
.ui-date-picker-wrap_mod-a {
  position: relative;
  display: flex;
  height: 32px;
  border-radius: 6px;
  border: 1px solid var(--grays-200, #DADDE1);
  background: var(--White, #FFF);
}
.ui-date-picker {
  max-width: 236px;
  border: none;
  padding-left: 36px;
  &:disabled,
  &[readonly] {
    background-color: transparent;
  }
  &__ic {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 36px;
    height: 100%;
    padding: 5px 8px;
  }
}

.ui-select-wrap {
  position: relative;
  flex-grow: 1;
}

.ui-select {
  &:not(.ui-select_arr-default) {
    &:after {
      flex: 0 0 auto;
      width: 7px;
      height: 7px;
      margin: 0 2px 0 12px;
      border: 1.25px solid #6C7685;
      border-top: none;
      border-right: none;
      transform: rotate(-45deg);
    }
  }
  &:hover {
    background-color: #F3F7F3;
  }

  &_w-bg {
    background-color: #F5F7FA;
    border-color: #EDEEF0;
  }

  &_inner {
    border-color: transparent;
  }
  &_w-border {
    border-color: #D0D3D8;
  }
  &_sm {
    padding-top: 6px;
    padding-bottom: 6px;
  }
  &_lg {
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 15px;
  }

  &.form-control-sm {
    padding: 4px 16px 4px 8px;
    &:not(.ui-select_arr-default) {
      &:after {
        position: relative;
        top: -2px;
      }
    }
  }

  .badge {
    height: auto;
    padding: 3px 6px;
    font-size: 13px;
    line-height: 1;
  }
}

.ui-select-page {
  padding: 9px 12px;
  border: 1px solid #DADDE1;
  background-color: #fff;
  border-radius: 7px;
  color: #1C2229;
  font-weight: 600;
  font-size: 16px;
}

.ui-select-badge {
  padding: 0 !important;
  &:after {
    content: '' !important;
    display: inline-block !important;
  }
  .ui-badge {
    padding-right: 20px;
  }
}

.ui-select-surveyControl {
  margin-right: 10px;
  .form-control {
    &:not(.ui-select) {
      padding-right: 120px;
    }
  }
  .bootstrap-select {
    width: auto !important;
  }

  @include media-breakpoint-down(xs) {
    margin-right: 0;
    margin-bottom: 8px;
  }
}
.ui-select-group {
  display: flex;

  &__item {
    margin-right: 8px;
  }
}

.bootstrap-select {
  &.dropup {
    .ui-select:not(.ui-select_arr-default) {
      &::after {
        top: 1px;
        margin: 0 2px 0 12px;
        border: 1.25px solid #6C7685;
        border-top: none;
        border-right: none;
        transform: scale(1, -1) rotate(-45deg);
      }
    }
  }
  .ic {
    width: 20px;
    height: 20px;
    object-fit: contain;
  }
}

.custom-checkbox {
  &_right {

    padding-left: 0;
    padding-right: 24px;

    .custom-control-input {
      left: auto;
      right: 0;
    }
    .custom-control-label {
      width: 100%;
      &::after,
      &::before {
        left: auto;
        right: -1.5rem;
      }
    }
  }
}

.breadcrumb-wrap {
  flex: 0 0 auto;
  background-color: #FFF;
  @include media-breakpoint-down(md) {
