      }
    }

    &[data-nav-structure="true"] {
      &:not(.breadcrumb-wrap_one-elem) {
        .breadcrumb {
          direction: rtl;
          padding-right: 30px;
        }
        .breadcrumb-item {
          white-space: nowrap;
          display: inline;
          &:before {
            float: none;
            display: inline;
          }
        }
      }

      .breadcrumb {
        position: relative;
        width: 100%;
        margin-right: 16px;
        white-space: nowrap;
        display: block;
        overflow-x: hidden;
        &:after {
          content: '';
          position: absolute;
          top: 0;
          right: 0;
          width: 24px;
          height: 24px;
          background: #fff url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M16.643 10.5H7.35729C7.14852 10.5 7.05231 10.759 7.21066 10.8948L11.8535 14.8743C11.9378 14.9466 12.0624 14.9466 12.1468 14.8743L16.7896 10.8948C16.948 10.759 16.8517 10.5 16.643 10.5Z' fill='%236C7685'/%3E%3C/svg%3E%0A");
        }
      }
    }
  }
}


.form-group-wrap {
  position: relative;
}
.feedback {
  display: none;
  &__group {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 8px;
  }
  &__text {
    flex-grow: 1;
    font-size: 14px;
    line-height: 125%;
    color: #D92D20;
  }
  &__badge {
    padding: 2px 8px;
    border-radius: 3px;
    font-size: 13px;
    line-height: 15px;
  }

  .progress {
    height: 5px;
    margin-top: 12px;
    background: #EDEEF0;
    border: 0.5px solid #EDEEF0;
    border-radius: 10px;
  }
  .progress-bar {
    border-radius: 5px;
  }
  &.has-error,
  &.has-success {
    display: block;
  }
}
.form-group {
  &.has-error,
  &.has-success {
    .feedback {
      display: block;
    }
  }
}

.form-group {
  &.is-invalid,
  &.is-valid,
  &.is-warning {
    .feedback {
      display: block;
    }
  }
}
.form-control-date {
  &[readonly] {
    background-color: transparent;
  }
}
.form-group {
  &.is-invalid {
    .form-control {
      border-color: #D92D20;
    }
    .custom-control-label {
      color: #D92D20;
      &:before {
        border-color: #D92D20;
      }
    }

    .feedback {
      &__badge {
        color: #D92D20;
        background: #FBEAE9;
      }
    }
  }
  &.is-valid {
    .form-control {
      border-color: #2F8733;
    }
    .custom-control-label {
      color: #2F8733;
      &:before {
        border-color: #2F8733;
      }
    }
    .feedback {
      &__badge {
        color: #128F50;
        background-color: #E8F7F0;
      }
    }

    .progress-bar {
      width: 100%;
      background-color: #16B364;
    }
  }

  &.is-warning {
    .feedback {
      &__badge {
        color: #C67307;
        background-color: #FEF4E6;
      }
    }

    .progress-bar {
      width: 50%;
      background-color: #F9A63A;
    }
  }
}
.custom-control-label {
  &.st-disabled {
    opacity: 0.4;
  }
}
.ui-input-clear {
  width: 100%;
  padding-left: 12px;
  border: none;
  background-color: transparent;
  font-size: 14px;
  line-height: 140%;
  color: #1C2229;
  &:focus,
  &:focus-visible {
    box-shadow: none;
  }
}
.ui-label {
  position: relative;
  display: block;
  padding: 2px 20px 2px 6px;
  background: #F5F7FA;
  border-radius: 2px;
  font-size: 14px;
  line-height: 140%;
  color: #1C2229;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  .close {
    position: absolute;
    top: -6px;
    right: 6px;
    bottom: 0;
    .ic {
      width: 12px;
      height: 12px;
    }
  }
}

.ui-labels-block {
  display: flex;
  border: 1px solid #D0D3D8;
  border-radius: 4px;
  align-items: center;
  justify-content: space-between;
  &__inner {
    padding: 5px 0 5px 5px;
    flex: 0 0 auto;
    width: 150px;
  }
  .ui-label {
    width: 100%;
    margin-bottom: 3px;
  }
  .dropdown {
    flex: 0 0 auto;
    width: 145px !important;
  }
  .bootstrap-select {
    position: relative;
  }
}

.ui-nav {
  .nav-link {
    height: 48px;
    display: flex;
    align-items: center;
    padding: 14px 24px 14px 20px;
    font-weight: 500;
    line-height: 140%;
    color: #1C2229;
    .ic {
      margin-right: 8px;
    }
    &.active,
    &:hover {
      box-shadow: inset 0 -2px 0 #2F8733;
    }
