      &:hover {
        border-radius: 4px;
        background: var(--main-green-50, #F3F7F3);
        text-decoration: none;
      }
    }
  }
  .dropdown {
    &:hover {
      border-radius: 4px;
      background: var(--main-green-50, #F3F7F3);
      text-decoration: none;
    }
  }
  .dropdown-toggle {
    padding: 8px 16px;
    font-size: 14px;
    border: none;
    background-color: transparent;
    transition: all .3s;
    color: var(--main-green-600, #2F8733);
    &:after {
      content: none;
    }
  }
  .dropdown-item {
    .ic {
      display: inline-block;
      margin-right: 10px;
    }
  }
  thead {
