      vertical-align: bottom;
      &:not(:last-child) {
        border-right: 1px solid var(--grays-100, #EDEEF0);
      }
      span {
        display: flex;
        align-items: flex-end;
      }
      .dropdown {
        position: absolute;
        right: 12px;
        bottom: 8px;
      }
