.ui-notific {
  &__title {
    padding: 12px 20px;
    color: var(--Grays-900, #1C2229);
    font-size: 18px;
    font-weight: 500;
    line-height: 140%;
    border-bottom: 1px solid var(--Grays-100, #EDEEF0);
  }
  &__body {
    max-height: 80vh;
    padding: 8px;
  }
  &__item {
    position: relative;
    padding: 12px;
    border-radius: 4px;

    &:not(:last-child) {
      border-bottom: 1px solid var(--Grays-50, #F5F7FA);
    }
    &.st-new {
      &:after {
        content: '';
        position: absolute;
        top: 4px;
        bottom: 4px;
        right: 0;
        display: block;
        width: 3px;
        border-radius: 4px;
        background: var(--Main, #2F8733);
      }
    }
  }
  &__link {
    display: -webkit-box;
    overflow: hidden;
    color: var(--Grays-900, #1C2229);
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    font-size: 14px;
    font-style: normal;
    line-height: 140%;

    &:hover {
      text-decoration: none;
    }
  }

  .dropdown-menu {
    width: 430px;
    border-radius: 8px;
    border: 1px solid var(--Grays-100, #EDEEF0);
    background: var(--White, #FFF);
    box-shadow: 0px 5px 8px -4px rgba(0, 0, 0, 0.03), 0px 15px 24px -4px rgba(0, 0, 0, 0.04), 0px 4px 20px 0px rgba(31, 34, 41, 0.06);
  }
  .dropdown-toggle::after {
    content: none;
  }
  .ui-badge {
    margin-right: 5px;
  }


  @include media-breakpoint-down(sm) {
    .dropdown-menu {
      width: auto;
      min-width: 320px;
    }
  }
}

