.b-login {
  position: relative;
  min-height: 100%;
  display: flex;
  &__logo {
    position: absolute;
    top: 32px;
    left: 32px;
    display: flex;
    align-items: center;
  }
  &__logo-title {
    margin-right: 2px;
    font-weight: 700;
    font-size: 18px;
    line-height: 20px;
    color: #1C2229;
  }
  &__second {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100%;
    width: 720px;
    flex: 0 0 auto;
    padding: 32px;
    background: #EDEEF0 url(../img/content/b-login/item-1.png) no-repeat;
    background-size: cover;
  }
  &__info {
    max-width: 563px;
    font-weight: 700;
    font-size: 38px;
    line-height: 45px;
    letter-spacing: 0.396789px;
    color: #475467;
  }
  &__language {
    position: absolute;
    top: 32px;
    right: 32px;
    &.ui-language {
      width: auto;
    }
  }


  &__advant {
    width: 440px;
    padding: 24px 28px 20px;
    background-color: #FFF;
    box-shadow: 0px 0px 6px -2px rgba(0, 0, 0, 0.03), 0px 6px 16px -4px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
  }
  &__advant-title {
    margin-bottom: 20px;
    font-weight: 500;
    font-size: 24px;
    line-height: 150%;
    color: #1C2229;
  }
  &__advant-list {
    margin-bottom: 0;
    padding-left: 0;
    list-style-type: none;
    font-size: 16px;
    line-height: 140%;
    color: #1C2229;
    li {
      position: relative;
      margin-bottom: 13px;
      padding-left: 36px;
      line-height: 140%;
      &:after {
        position: absolute;
        left: 0;
        top: -1px;
        display: block;
        content: '';
        width: 24px;
        height: 24px;
        background-image: url('data:image/svg+xml,<svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M11.9993 22C17.4993 22 21.9993 17.5 21.9993 12C21.9993 6.5 17.4993 2 11.9993 2C6.49927 2 1.99927 6.5 1.99927 12C1.99927 17.5 6.49927 22 11.9993 22Z" fill="%2345C283" stroke="white" stroke-width="1.25" stroke-linecap="round" stroke-linejoin="round"/><path d="M7.75073 12.0001L10.5807 14.8301L16.2507 9.17017" stroke="white" stroke-width="1.25" stroke-linecap="round" stroke-linejoin="round"/></svg>');
      }
    }
  }


  @include media-breakpoint-down(lg) {
    &__second {
      width: 50%;
    }
  }

  @include media-breakpoint-down(md) {
    padding-top: 136px;
    padding-bottom: 30px;
    &__second {
      display: none;
    }
    &__logo {
      top: 24px;
      left: 24px;
    }
    &__language {
      top: 34px;
      right: 12px;
    }
  }
}

.b-login-form {
  display: flex;
  flex: 1 0 auto;
  align-items: center;
  max-width: 720px;
  padding: 30px 0 30px 193px;
  &__inner {
    width: 340px;
    margin-left: auto;
    margin-right: auto;
    text-align: left;
  }
  &__title {
    margin-bottom: 4px;
    font-weight: 500;
    font-size: 32px;
    line-height: 130%;
    color: #111722;
  }
  &__info {
    padding-bottom: 20px;
    font-size: 16px;
    line-height: 1.3;
  }
  &__separator {
    display: flex;
    color: #6C7685;
    align-items: center;
    margin-top: 24px;
    margin-bottom: 17px;
    line-height: 1.25;
    &:after,
    &:before {
      content: '';
      flex-grow: 1;
      height: 1px;
      background-color: #EDEEF0;
    }
    &:after {
      margin-left: 12px;
    }
    &:before {
      margin-right: 12px;
    }
  }
  &__link {
    font-size: 13px;
  }
  &__group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 11px;
    margin-bottom: 40px;
  }
  &__btn {
    margin-top: 40px;
    margin-bottom: 6px;
  }
  &__note {
    a {
      text-decoration: underline;
    }
  }

  @include media-breakpoint-down(lg) {
    width: 50%;
    padding-left: 0;
    justify-content: center;
  }

  @include media-breakpoint-down(md) {
    width: 100%;
    max-width: 100%;
    padding-top: 0;
    padding-bottom: 0;
    &__inner {
      width: 100%;
      max-width: 340px;
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}
