.b-modal-upload {
  &__search {
    position: relative;
    width: 272px;
    margin-left: auto;
    stroke: #9198A4;
  }
  &__input {
    width: 100%;
    padding-left: 36px;
  }
  &__search-btn {
    position: absolute;
    left: 8px;
    top: 6px;
    bottom: 6px;
    width: 20px;
    height: 20px;
    padding: 0;
    border: none;
    background-color: #FFF;
  }
  &__list {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-left: -6px;
    margin-right: -6px;
  }
  &__item {
    flex: 0 0 auto;
    width: 128px;
    margin: 20px 6px 0;
    padding: 4px;
  }
  &__img {
    width: 120px;
    height: 120px;
    fit-object: cover;
    border-radius: 4px;
    overflow: hidden;
  }
  &__caption {
    display: block;
    margin-top: 10px;
    line-height: 125%;
    color: #1C2229;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  &__size {
    display: block;
    margin-top: 4px;
    font-size: 13px;
    line-height: 15px;
    color: #394352;
  }

  .modal-dialog {
    width: 870px;
    max-width: 100%;
  }
  .modal-header {
    padding-bottom: 0;
  }


  @include media-breakpoint-down(lg) {
    .modal-dialog {
      width: 100%;
    }

    &__search {
      width: 100%;
    }

  }
}
