.b-basket-card {
  padding: 16px 16px 20px;
  border-bottom: 1px solid #DADDE1;
  color: #1C2229;
  font-size: 13px;
  &__wrap {
    display: flex;
    justify-content: space-between;
  }
  &__media {
    position: relative;
    width: 100px;
    flex: 0 0 auto;
    height: 100px;
    margin-left: 10px;
    border-radius: 4px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title {
    display: block;
    margin-bottom: 6px;
    font-size: 18px;
    font-weight: 700;
    color: #1C2229;
  }
  &__info {
    color: #2B323E;
  }
  &__footer {
    margin-top: 8px;
  }
  &__weight {
    color: #394352;
  }
  &__price {
    font-size: 28px;
    font-weight: 700;
    color: #2F8733;
  }
  &__group {
    display: flex;
    align-items: center;
  }
  &__btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    padding: 0;
    background-color: #EDEEF0;
    border-radius: 7px;
    border: none;
  }
}

.b-basket-total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  border-bottom: 1px solid #EDEEF0;
  background-color: #F5F7FA;
  font-weight: bold;
  font-size: 28px;
}
