.b-outside {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100%;
  background: var(--grays-25, #F9FAFB);

  &__head {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    background-size: cover;
  }
  &__header {
    background: #538CE4;
    min-height: 128px;
    padding: 16px;

    @media (max-width: 1023px) {
      height: 102px;
    }
  }
  &__title {
    margin-bottom: 14px;
    color: var(--grays-900, #1C2229);
    font-size: 32px;
    font-weight: 700;
    line-height: 118%;
  }
  &__info {
    color: var(--grays-900, #1C2229);
    font-size: 18px;
    line-height: 140%;
  }

  &__date {
    color: var(--grays-500, #6C7685);
    font-size: 18px;
    line-height: 140%;
  }


  &__languages {
    position: absolute;
    top: 16px;
    right: 16px;
    background: #fff;
    border-radius: 4px;
    z-index: 51;


    @media (max-width: 1023px) {
      max-width: 54px;

      .filter-option {
        font-size: 0;
      }

      button.btn.dropdown-toggle.btn-light {
        padding: 4px 8px 4px 6px;
      }
    }
  }

  &__content {
    position: relative;
    max-width: 798px;
    width: 100%;
    margin: 60px auto;
    padding-left: 15px;
    padding-right: 15px;

    .b-frame-logo {
      justify-content: flex-start;
      margin: 0 24px 24px 0;
    }
  }

  &__inner {
    padding: 24px;
    border-radius: 20px;
    border: 1px solid var(--grays-100, #EDEEF0);
    background: var(--White, #FFF);
  }

  &__step1 {
    h3 {
      margin-bottom: 32px;
    }
  }

  &__step2 {
    h1 {
      margin-bottom: 18px;
    }

    p {
      margin-bottom: 32px;
      font-size: 18px;
    }
  }

  &__step3 {
    h1 {
      margin-bottom: 18px;
    }

    p {
      margin-bottom: 32px;
      font-size: 18px;
    }
  }

  &__group {
    padding: 16px 0 24px;

    .label-note {
      margin-left: 2px;
    }

    .b-control-options__title {
      font-size: 16px;
      line-height: 140%;

      .label-note {
        font-size: 14px;
      }
    }

    .form-group {
      max-width: 352px;
    }

    .form-group > label {
      color: #475467;
      margin-bottom: 16px;
    }

    .form-group textarea {
      min-height: 120px;
    }

    .form-group .b-frame__input-wrap .select-phone-code .btn {
      padding: 13px;
    }

    .ui-date-picker-wrap {
      background: transparent;
    }

    .ui-upload {
      max-height: 124px;
    }

    .ui-upload__btn {
      background: #F5F7FA;
      color: #2B323E;
    }

    .b-modal-type__signature {
      max-width: 100%;
    }

    &_full {
      max-width: 100%;
    }
  }

  &__progress {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    z-index: 50;
  }
  &__progressbar {
    display: flex;
    justify-content: space-between;
    width: 768px;
    padding-top: 16px;
    padding-bottom: 8px;
    background-color: #f9fafb;

    &.fixed {
      position: fixed;
      top: 0;
      left: 50%;
      max-width: 764px;
      width: 100%;
      margin-top: 0;
      padding: 0;
      background: white;
      transform: translateX(-50%);
    }
  }
  &__progressbar-item {
    position: relative;
    flex-grow: 1;
    height: 21px;
    text-align: center;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      display: block;
      height: 4px;
      border-radius: 2px;
      background: var(--grays-200, #DADDE1);
    }

    &:not(:last-child) {
      margin-right: 2px;
    }
    &.active {
      &:after {
        background: var(--success-600, #16B364);
      }
    }
  }
  &__progressbar-percent {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    display: inline-block;
    margin-top: 6px;
    color: var(--success-600, #16B364);
    font-size: 12px;
    font-weight: 500;
    line-height: 125%;
  }

  &__btns-group {
    .btn {
      margin-right: 12px;
    }
  }

  &__copyright {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 16px;
    padding-bottom: 16px;
    color: var(--grays-800, #2B323E);
    font-size: 13px;
    font-weight: 500;
    line-height: 120%;
    background: #FAFAFA;

    .ic {
      width: 16px;
      height: 16px;
      margin-left: 6px;
      margin-right: 6px;
    }

    a {
      display: inline-flex;
      align-items: center;
      color: var(--grays-800, #2B323E);
    }
  }

  .b-frame-icons {
    &__labels {
      padding-left: 12px;
      padding-right: 12px;
    }
    &__list {
      &[data-selected-icons="default"] {
        .b-frame-icons {
          &__item {
            .ic {
              fill: #B5BBC2;
            }
            &_like {
              border-color: #B5BBC2;
            }
          }
        }

      }
      &[data-selected-icons="1"] {
        .b-frame-icons {
          &__item {
            &:nth-child(n+2) {
              .ic {
                fill: #B5BBC2;
              }
            }
            &_like {
              &:first-child {
                fill: #AE241A;
                border-color: #AE241A;
              }
              &:last-child {
                border-color: #B5BBC2;
              }
            }
          }
        }
      }
      &[data-selected-icons="2"] {
        .b-frame-icons {
          &__item {
            &:nth-child(n+3) {
              .ic {
                fill: #B5BBC2;
              }
            }
            &_like {
              &:first-child {
                fill: #B5BBC2;
                border-color: #B5BBC2;
              }
              &:last-child {
                fill: #128F50;
                border-color: #128F50;
              }
            }
          }
        }
      }
      &[data-selected-icons="3"] {
        .b-frame-icons {
          &__item {
            &:nth-child(n+4) {
              .ic {
                fill: #B5BBC2;
              }
            }
          }
        }
      }
      &[data-selected-icons="4"] {
        .b-frame-icons {
          &__item {
            &:nth-child(n+5) {
              .ic {
                fill: #B5BBC2;
              }
            }
          }
        }
      }
      &[data-selected-icons="5"] {
        .b-frame-icons {
          &__item {
            &:nth-child(n+6) {
              .ic {
                fill: #B5BBC2;
              }
            }
          }
        }
      }
      &[data-selected-icons="6"] {
        .b-frame-icons {
          &__item {
            &:nth-child(n+7) {
              .ic {
                fill: #B5BBC2;
              }
            }
          }
        }
      }
      &[data-selected-icons="7"] {
        .b-frame-icons {
          &__item {
            &:nth-child(n+8) {
              .ic {
                fill: #B5BBC2;
              }
            }
          }
        }
      }
      &[data-selected-icons="8"] {
        .b-frame-icons {
          &__item {
            &:nth-child(n+9) {
              .ic {
                fill: #B5BBC2;
              }
            }
          }
        }
      }
      &[data-selected-icons="9"] {
        .b-frame-icons {
          &__item {
            &:nth-child(n+10) {
              .ic {
                fill: #B5BBC2;
              }
            }
          }
        }
      }
    }
    &__item {
      input[type="radio"] {
        width: 0;
        height: 0;
        opacity: 0;
        position: absolute;
      }
      &_count{
        &:after {
          content: none;
        }
        label {
          position: relative;
          &:after {
            position: absolute;
            top: 53%;
            left: 50%;
            -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
            content: counter(item);
            line-height: 1.2;
          }
        }
      }
    }
  }

  .b-control-options__title {
    display: block;

    .label-note {
      display: inline;
    }
  }

  @include media-breakpoint-down(sm) {
    &__btns-group {
      .btn {
        width: 100%;
        margin-right: 0;
        + .btn {
          margin-top: 6px;
        }
      }
    }
  }
}

.b-outside-admin {
  border-radius: 10px;
  border: 1px solid var(--grays-200, #DADDE1);
  background: var(--grays-25, #F9FAFB);
  overflow: hidden;
}


.b-outside-qr {
  display: flex;
  align-items: center;
  padding-top: 21px;
  padding-bottom: 21px;
  &__img {
    margin-right: 12px;
  }
  &__info {
    color: var(--grays-900, #1C2229);
    font-size: 16px;
    font-weight: 500;
    line-height: 140%;
  }
  &__text {
    margin-bottom: 12px;
  }
  &__link {
    display: inline-block;
    color: var(--external-survey-accent-blue, #0359FF);
  }
  &__group {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }


  @include media-breakpoint-down(sm) {
    flex-wrap: wrap;
  }
}


.b-outside-result {
  padding-bottom: 24px;
  &__header {
    padding: 12px 0;
    border-bottom: 1px solid var(--grays-100, #EDEEF0);
    color: var(--grays-900, #1C2229);
    text-align: center;
    font-size: 20px;
    font-weight: 700;
    line-height: 140%;
  }
  &__group {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 20px;
    padding-bottom: 16px;
  }
  &__block {
    margin-right: 16px;
    padding-bottom: 8px;
  }
  &__title {
    color: var(--grays-900, #1C2229);
    font-size: 16px;
    font-weight: 500;
    line-height: 140%;
  }
  &__info {
    color: var(--grays-600, #475467);
    font-size: 12px;
    line-height: 125%;
  }
  &__footer {
    margin-top: 12px;
    padding: 12px;
    border-radius: 8px;
    background: var(--grays-50, #F5F7FA);
    color: var(--grays-600, #475467);
    font-size: 14px;
    line-height: 125%;
  }
  &__current {
    margin-left: 4px;
    font-weight: 500;
    color: var(--grays-1000, #0E1115);
  }


  @include media-breakpoint-down(md) {
    &__group {
      flex-wrap: wrap;
    }
  }
}
