.b-final {
  &__wrap {
    padding: 36px 24px 20px;
    height: calc(100vh - 60px);
  }

  &__label {
    position: absolute;
    top: 12px;
    right: 12px;
    padding: 3px 12px;
    border-radius: 30px;
    background: #EDEEF0;
    color: #475467;
    font-size: 11px;
  }

  &__title {
    margin-bottom: 4px;
    text-align: center;
    font-weight: 500;
    font-size: 20px;
    letter-spacing: 0.002em;
    color: #2B323E;
  }
  &__info {
    font-size: 14px;
    line-height: 140%;
    text-align: center;
    color: #2B323E;
  }
  &__group {
    margin-top: 36px;
    display: flex;
    flex-wrap: wrap;
    margin-right: -16px;
  }
  &__img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;
  }
  &__item {
    width: 25%;
    padding-right: 16px;
    margin-bottom: 16px;
    flex: 0 0 auto;

    &.st-inactive {
      pointer-events: none;
    }
    &.st-inactive,
    &.st-beta {
      .b-final__name,
      .b-final__info {
        color: #6C7685;
      }
    }
  }
  &__link {
    padding: 0;
    border: none;
    background-color: transparent;
    text-decoration: underline;
    color: #6C7685;
    &:hover {
      color: var(--main-green-600, #2F8733);
    }
  }
  &__inner {
    position: relative;
    display: block;
    height: 100%;
    padding: 16px;
    background: #FFFFFF;
    border: 1px solid #DADDE1;
    border-radius: 6px;
    transition: all .3s;
    &:hover {
      text-decoration: none;
      border-color: var(--main-green-600, #2F8733);
    }
  }

  &__name {
    margin-top: 20px;
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: #1C2229;
  }
  &__text {
    margin-top: 4px;
    font-size: 14px;
    line-height: 125%;
    color: #475467;
  }


  @include media-breakpoint-down(md) {
    &__wrap {
      height: calc(100vh - 100px);
    }
    &__item {
      width: 50%;
    }
  }

  @include media-breakpoint-down(sm) {
    &__item {
      width: 100%;
    }
  }
}
