  color: #2B323E;
}
.b-basket-card__footer {
  margin-top: 8px;
}
.b-basket-card__weight {
  color: #394352;
}
.b-basket-card__price {
  font-size: 28px;
  font-weight: 700;
  color: #2F8733;
