.b-modal-report {
  &__title {
    margin-bottom: 4px;
    color: var(--grays-900, #1C2229);
    font-size: 18px;
    font-weight: 500;
    line-height: 140%;
    text-align: center;
  }
  &__text {
    margin-bottom: 24px;
    text-align: center;
    color: var(--grays-900, #1C2229);
    font-size: 13px;
    line-height: 140%;
  }
  &__card {
    margin-bottom: 12px;
    border-radius: 8px;
    border: 1px solid var(--grays-100, #EDEEF0);
  }
  &__header {
    display: flex;
    align-items: center;
    padding: 12px;
    border-bottom: 1px solid var(--grays-100, #EDEEF0);
    color: var(--grays-900, #1C2229);
    font-size: 14px;
    font-weight: 500;
    line-height: 125%;
    .ic {
      width: 16px;
      height: 16px;
      margin-right: 12px;
    }
  }
  &__body {
    padding: 12px;
  }
  &__note {
    color: var(--grays-600, #475467);
    font-size: 13px;
    line-height: 140%;
  }

  @include media-breakpoint-down(sm) {
    &__add {
      width: 100%;
      margin-top: 8px;
    }
  }
}
