.b-modal-call {
  .modal-body {
    border-radius: 16px 16px 0 0;
  }
  &__title {
    margin-bottom: 24px;
    margin-top: 40px;
    font-size: 28px;
    line-height: 37px;
    font-weight: 600;
    text-align: center;
  }
  &__fig {
    position: absolute;
    left: calc(50% - 40px);
    top: -40px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 80px;
    height: 80px;
    background-color: #2F8733;
    z-index: 1;
    border-radius: 50%;
    color: #FFF;
    font-size: 20px;
    font-weight: 500;

    .ic {
      fill: #fff;
    }
    .ui-spinner {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(0.38) rotate(-90deg);
      &:after {
        content: none;
      }
      #svg #bar {
        stroke: #fff;
      }
      #svg circle {
        stroke-width: 8px;
        stroke: none;
      }
    }

    &.st-success {
      background-color: #7ED321;
    }
    &.st-neutral {
      background-color: #FFCE00;
    }
    &.st-danger {
      background-color: #D24103;
    }
  }
}
