.b-confirm {
  display: flex;
  flex: 1 0 auto;
  align-items: center;
  justify-content: center;
  max-width: 720px;
  padding: 50px 20px;
  &__inner {
    width: 580px;
    text-align: center;
  }
  &__img {
    margin-bottom: 36px;
  }
  &__title {
    margin-bottom: 16px;
    font-weight: 500;
    font-size: 32px;
    line-height: 130%;
    color: #111722;
  }
  &__info {
    margin-bottom: 28px;
    font-size: 16px;
    line-height: 140%;
    color: #111722;
  }
  &__form-resend {
    margin-top: 36px;
  }

  @include media-breakpoint-down(lg) {
    width: 50%;
  }

  @include media-breakpoint-down(md) {
    width: 100%;
    max-width: 100%;
    padding-top: 0;
    padding-bottom: 0;
    &__inner {
      width: 100%;
      max-width: 450px;
    }
    &__btn {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
}
