.b-modal-structure {
  .modal-dialog {
    display: flex;
    align-items: flex-end;
    max-width: 100%;
    width: 100%;
    height: 100%;
    margin: 0;
  }
  .modal-content {
    border-radius: 0;
    border: none;
  }

  &__header {
    display: flex;
    padding: 12px 12px 12px 16px;
    justify-content: space-between;
    .close {
      .ic {
        width: 24px;
        height: 24px;
      }
    }
  }
  &__title {
    font-weight: 500;
    font-size: 18px;
    line-height: 140%;
    color: #1C2229;
  }
  &__body {
    padding: 8px 8px 15px;
  }
  &__footer {
    padding: 16px 12px;
    border-top: 1px solid #D6D6D6;
  }
}
