.b-frame {
  &.st-pdf { // For generate pdf
    border: none;
    .b-frame__header {
      display: none;
    }
    .b-frame__title-wrap {
      max-width: 100% !important;
    }
    .b-frame__subtitle {
      display: block;
    }
    .b-frame__subtitle,
    .b-frame__title-wrap {
      line-height: 1.4;
      color: inherit;
    }
  }

  &.st-a3 {
    width: 25cm;
    height: 38cm;
    margin: 2cm;
    padding-top: 300px;
    padding-bottom: 200px;
    padding-left: 6cm;
    padding-right: 6cm;

    .b-frame__body {
      transform: scale(1.7);
    }

    &.st-landscape {
      width: 38cm;
      height: 25cm;
      padding-top: 200px;
      padding-bottom: 100px;
      padding-left: 8cm;
      padding-right: 8cm;
    }
  }
  &.st-a4 {
    width: 17cm;
    height: 25cm;
    margin: 2cm;
    padding-top: 100px;
    &.st-landscape {
      height: 17cm;
      width: 25cm;
    }
  }
  &.st-a5 {
    width: 12.4cm;
    height: 19cm;
    margin: 1cm;

    &.st-landscape {
      width: 19cm;
      height: 12.4cm;
    }
  }
  &.st-a6 {
    width: 8.5cm;
    height: 12.6cm;
    margin: 1cm;
    .b-frame__qr-img img {
      width: 120px;
      height: 120px;
    }
    .b-frame__title {
      font-size: 14px;
    }
    .b-frame-logo__name {
      font-size: 14px;
    }
    .b-frame-logo__img {
      width: 45px;
      height: 45px;
    }
    .b-frame-icons__item img {
      width: 30px;
      height: 30px;
    }
    .b-frame_qr .b-frame-icons {
      margin-bottom: 0;
    }
    .b-frame-logo {
      padding-top: 30px;
    }
    .b-frame__title-wrap {
      width: 5.5cm;
    }
    .b-frame__logo-dev img {
      width: 100px;
    }
    .b-frame__subtitle,
    .b-frame__title-wrap {
      font-size: 10px;
    }
    .b-frame-icons {
      margin-bottom: 30px;
    }

    &.st-landscape {
      width: 12.6cm;
      height: 8.5cm;
      .b-frame-logo__name {
        font-size: 12px;
      }
      .b-frame__content {
        padding-top: 30px;
        padding-bottom: 0;
      }
      .b-frame__logo-dev {
        margin-top: 10px;
      }
    }
  }
}
