.b-modal-type {
  &__search {
    display: flex;
    flex-grow: 1;
    align-items: center;
    padding: 16px 32px 16px 0;
    .ic {
      margin-right: 8px;
      stroke: #6C7685;
    }
    input {
      flex-grow: 1;
      height: 100%;
      border: none;
      background-color: #FFF;
    }
  }
  &__sidebar {
    width: 250px;
    max-height: calc(100vh - 112px);
    flex: 0 0 auto;
    padding-top: 12px;
    padding-bottom: 12px;
    border-right: 1px solid var(--grays-100, #EDEEF0);
    overflow-x: hidden;
  }
  &__content {
    width: calc(100% - 250px);
    max-height: calc(100vh - 112px);
    flex: 0 0 auto;
  }
  &__wrap {
    max-width: 360px;
    .ui-date-picker {
      max-width: 100%;
    }
  }
  &__main {
    padding: 20px 20px 30px;
    flex-grow: 1;
  }
  &__title {
    margin-bottom: 6px;
    color: var(--grays-900, #1C2229);
    font-size: 20px;
    line-height: 140%;
    letter-spacing: 0.04px;
  }
  &__title2 {
    margin-bottom: 6px;
    color: var(--grays-900, #1C2229);
    font-size: 15px;
    font-weight: 500;
    line-height: 144%;
  }
  &__text {
    margin-bottom: 16px;
    color: var(--grays-700, #394352);
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
  }
  &__btn {
    .ic {
      width: 18px;
      height: 18px;
      transform: rotate(180deg);
      stroke: #fff;
    }
  }
  &__example {
    position: relative;
    min-height: 250px;
    padding: 32px 20px 24px;
    border-top: 1px solid var(--grays-100, #EDEEF0);
  }
  &__label {
    position: absolute;
    top: -8px;
    left: 20px;
    display: inline-block;
    padding: 2px 6px;
    border-radius: 4px;
    background: var(--grays-100, #EDEEF0);
    color: var(--grays-500, #6C7685);
    font-size: 11px;
    font-weight: 600;
    line-height: 125%;
    letter-spacing: 0.5px;
    z-index: 1;
  }

  &__signature {
    max-width: 210px;
    padding: 20px 8px;
    border-radius: 8px;
    border: 1px dashed var(--grays-300, #B5BBC2);
    background: var(--White, #FFF);
    text-align: center;

    img {
      position: relative;
      z-index: 1;
    }
  }
  &__signature-text {
    position: relative;
    margin-top: -10px;
    color: var(--grays-400, #9198A4);
    font-size: 12px;
    line-height: 125%;
    &:before {
      content: '';
      position: absolute;
      top: -13px;
      left: 12px;
      right: 12px;
      height: 1px;
      background: var(--grays-100, #EDEEF0);
    }
  }
  &__matrix {
    width: 384px;
    td,
    th {
      padding: 6px 12px;
      color: var(--grays-900, #1C2229);
      font-size: 13px;
      font-weight: normal;
      line-height: 140%;
      border-bottom: 1px solid var(--grays-100, #EDEEF0);
      border-right: 1px solid var(--grays-100, #EDEEF0);
      text-align: center;
      &:first-child {
        text-align: left;
        border-left: 1px solid var(--grays-100, #EDEEF0);
      }
    }
    th {
      border-top: 1px solid var(--grays-100, #EDEEF0);
    }
  }

  &__rank-item {
    cursor: grab;
    &:active {
      cursor: grabbing;
    }
    &:not(:last-child) {
      margin-bottom: 8px;
    }

    &.st-drag {
      .b-modal-type__rank-btn {
        border-style: dotted;
      }
    }
  }
  &__rank-btn {
    display: inline-flex;
    align-items: center;
    padding: 8px 12px 8px 8px;
    border-radius: 5px;
    border: 1px solid var(--grays-200, #DADDE1);
    color: var(--grays-900, #1C2229);
    font-size: 16px;
    font-weight: 400;
    line-height: 1;
  }
  &__rank-number {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 10px;
    padding: 1px 4px;
    color: #FFF;
    font-size: 12px;
    font-weight: 600;
    border-radius: 4px;
    background: var(--Blue, #0D7CF2);
  }

  .modal-dialog {
    max-width: 700px;
  }
  .modal-body {
    padding: 0;
    display: flex;
    justify-content: space-between;
  }

  .modal-header {
    align-items: center;
    padding: 0 12px;
  }
  .modal-body {
    padding: 0;
    display: flex;
    justify-content: space-between;
  }
  .tab-pane {
    flex-direction: column;
    justify-content: space-between;
    min-height: 100%;

    &.active {
      display: flex;
    }
  }
  .nav-item {
    width: 100%;
  }
  .nav-link {
    display: flex;
    align-items: center;
    padding: 10px 12px;
    color: var(--grays-700, #394352);
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
    &.active {
      background: var(--main-green-50, #F3F7F3);
    }

    .ic {
      width: 18px;
      flex: 0 0 auto;
      margin-right: 10px;
    }
  }

  @include media-breakpoint-down(sm) {
    &__content {
      display: none;
    }
    &__sidebar {
      width: 100%;
      max-height: 100%;
    }
  }

  @media (max-height:600px) {
    &__example {
      min-height: 0;
    }
  }
}
