.ui-forms-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100%;

  @include media-breakpoint-down(sm) {
    height: 100%;
    align-items: flex-start;
  }
}

.ui-forms {
  width: 440px;
  padding: 20px;
  &__title {
    font-weight: 500;
    font-size: 24px;
    line-height: 140%;
    color: #1C2229;
  }
  &__subtitle {
    margin-bottom: 40px;
    font-size: 15px;
    line-height: 140%;
    color: #475467;
  }
  &__footer {
    margin-top: 48px;
    background-color: #FFF;
  }

  @include media-breakpoint-down(sm) {
    height: 100%;
    width: 100%;
    max-width: 440px;
    display: flex;
    flex-direction: column;
    padding-top: 16px;
    &__main {
      flex-grow: 1;
    }
    &__subtitle {
      margin-bottom: 36px;
    }
    &__footer {
      position: sticky;
      bottom: 0;
      z-index: 1;
    }
  }
}

.ui-forms-user {
  display: flex;
  align-items: center;
  margin-top: 16px;
  margin-bottom: 16px;
  padding: 12px 0 12px 12px;
  background: #F5F7FA;
  border-radius: 6px;
  &__main {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 0 0 auto;
    flex-grow: 1;
    width: calc(100% - 36px);
    padding-left: 12px;
  }
  &__img {
    flex: 0 0 auto;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    object-fit: contain;
    overflow: hidden;
  }
  &__inner {
    flex: 0 0 auto;
    width: calc(100% - 150px);
  }
  &__name {
    font-weight: 500;
    font-size: 15px;
    line-height: 140%;
    color: #1C2229;
  }
  &__email {
    line-height: 125%;
    color: #475467;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  &__select {
    .dropdown {
      width: auto !important;
    }
    .ui-select {
      background-color: transparent;
    }
  }
}
