.b-tree {
  position: relative;
  height: calc(100vh - 112px);
  background: #F9FAFB;
  overflow: hidden;

  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  &.st-dragging {
    cursor: move;
  }

  &__wrap {
    --tree-x: 0px;
    --tree-y: 0px;
    --tree-scale: .7;
    display: flex;
    min-width: 100%;
    min-height: 100%;
    padding: 14px;
    transform: scale(var(--tree-scale)) translate(var(--tree-x), var(--tree-y));
  }
  &__nav {
    display: none;
    position: absolute;
    right: 10px;
    top: 10px;
    box-shadow: 0px 2px 8px -2px #00000014;
    z-index: 100;
  }
  &__nav-btn {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 28px;
    height: 28px;
    padding: 0;
    background: #FFFFFF;
    border: 1px solid #DADDE1;
    border-radius: 4px;
    &.st-left {
      right: 56px;
      top: 14px;
      .ic {
        transform: rotate(-90deg);
      }
    }
    &.st-right {
      right: 0;
      top: 14px;
      .ic {
        transform: rotate(90deg);
      }
    }
    &.st-top {
      top: 0;
      right: 28px;
    }
    &.st-bottom {
      top: 28px;
      right: 28px;
      .ic {
        transform: rotate(180deg);
      }
    }
  }
  &__zoom {
    position: absolute;
    left: 20px;
    top: 20px;
    box-shadow: 0px 2px 8px -2px #00000014;
    border-radius: 4px;
    z-index: 100;
  }
  &__zoom-btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 28px;
    height: 28px;
    padding: 0;
    background: #FFFFFF;
    border: none;
    &:first-child {
      &:after {
        content: '';
        position: absolute;
        left: 4px;
        right: 4px;
        bottom: -1px;
        height: 1px;
        border-radius: 1px;
        background: #DADDE1;
        z-index: 1;
      }
    }
    .ic {
      transition: all .3s;
      stroke: #2B323E;
      stroke-width: 1.5;
    }
    &:hover {
      .ic {
        stroke: #2F8733;
      }
    }
  }

  &__list {}
  &__block {
    position: relative;

    &.st-widt-group {
      > .b-tree__subblock {
        &:after {
          content: '';
          position: absolute;
          left: 160px;
          top: 0;
          bottom: -15px;
          width: 1px;
          background-color: #DADDE1;
        }
      }
    }
  }
  &__subblock {
    position: relative;
    display: flex;
    margin-top: -10px;
    &:before {
      content: '';
      display: block;
      width: 320px;
      margin-right: 26px;
      flex: 0 0 auto;
    }
  }
  &__col {
    position: relative;
    margin-left: 14px;
    margin-right: 14px;
    &:before {
      content: '';
      position: absolute;
      left: calc(-50% + -56px);
      bottom: 100%;
      width: 370px;
      height: 57px;
      border-top-right-radius: 10px;
      border-top: 1px solid #DADDE1;
      border-right: 1px solid #DADDE1;
    }
    .b-tree {
      &__item {
        &:not(:last-child) {
          margin-bottom: 10px;

          &:after {
            content: '';
            position: absolute;
            left: 50%;
            top: 100%;
            width: 1px;
            height: 11px;
            background-color: #DADDE1;
          }
        }
      }
    }
  }
  &__group {
    display: flex;
    margin-top: 16px;
    padding-top: 130px;
    > .b-tree__block {
      margin-right: 66px;
      &:before {
        content: '';
        position: absolute;
      }

      &:not(:first-child) {
        margin-left: 80px;
      }
      &:first-child {
        &:before {
          left: 160px;
          bottom: 100%;
          width: 1px;
          height: 132px;
          background-color: #DADDE1;
        }
      }
      &:not(:last-child) {
        &:after {
          content: '';
          position: absolute;
          left: 160px;
          right: -310px;
          bottom: 100%;
          height: 132px;
          border-top-right-radius: 10px;
          border-top: 1px solid #DADDE1;
          border-right: 1px solid #DADDE1;
        }
      }
    }
  }
  &__item {
    position: relative;
    width: 320px;
    padding: 16px;
    border-radius: 8px;
    border: 1px solid #EDEEF0;
    background-color: #FFF;
    z-index: 1;
    transition: all .3s;
    user-select: none;

    &:hover {
      cursor: pointer;
      border-color: #2F8733;
      box-shadow: 0px 0px 6px 0px #167E2140;
    }
  }
  &__header {
    display: flex;
    align-items: center;
  }
  &__logo {
    margin-right: 12px;
    max-width: 80px;
    height: auto;
    object-fit: contain;
  }
  &__title {
    color: #1C2229;
    font-size: 16px;
    font-weight: 500;
    line-height: 22.4px;
  }
  &__btn-collapse {
    position: absolute;
    bottom: -16px;
    left: calc(50% - 12px);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    padding: 0;
    border-radius: 50%;
    border: 1px solid #DADDE1;
    background-color: #fff;

    &[aria-expanded="false"] {
      .ic {
        &.st-hide {
          display: none;
        }
      }
    }
    &[aria-expanded="true"] {
      .ic {
        &.st-show {
          display: none;
        }
      }
    }
    .ic {
      stroke: #394352;
    }

    &:hover {
      border-color: #2F8733;
      .ic {
        stroke: #2F8733;
      }
    }
  }

  &__progress {
    margin-top: 33px;
    overflow: visible;
    border-radius: 0;
    background-color: transparent;
    .progress-bar {
      position: relative;
      overflow: visible;
      &:first-child {
        border-top-left-radius: 23px;
        border-bottom-left-radius: 23px;
      }
      &:last-child {
        border-top-right-radius: 23px;
        border-bottom-right-radius: 23px;
      }
      &:not(:first-child) {
        box-shadow: -1px 0 0 #FFF;
      }
    }
    .progress-number {
      position: absolute;
      top: -17px;
      left: 0;
      color: #475467;
      font-size: 11px;
      line-height: 13.75px;
    }
  }

  .ui-badge-type {
    margin-bottom: 8px;
    padding: 2px 4px;
    font-size: 11px;
    line-height: 13.75px;
  }


  @include media-breakpoint-down(md) {
    &__zoom {
      left: 10px;
      top: 10px;
    }
  }
}
