.b-modal-tour {
  &__body {
    display: flex;
    align-items: center;
    padding: 48px 40px;
  }
  &__img {
    flex: 0 0 auto;
    width: 160px;
  }
  &__main {
    flex: 0 0 auto;
    width: calc(100% - 160px);
    padding-left: 40px;
  }
  &__title {
    margin-bottom: 12px;
    font-weight: 500;
    font-size: 22px;
    line-height: 120%;
    color: #101828;
  }
  &__info {
    line-height: 140%;
    color: #101828;
    p {
      margin-bottom: 0;
      &:not(:last-child) {
        margin-bottom: 12px;
      }
    }
  }
  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
    border-top: 1px solid #E0E6ED;
    .btn {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
  &__steps {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    font-size: 13px;
    line-height: 15px;
    color: #9198A4;
    text-align: center;
  }

  .close {
    position: absolute;
    top: 16px;
    right: 16px;
  }

  .modal-header {
    padding: 22px 60px 6px 20px;
    border-bottom: none;
    .close {
      top: 16px;
      right: 20px;
    }
  }
  .modal-title {
    font-size: 16px;
    line-height: 130%;
    letter-spacing: normal;
  }
  .modal-body {
    padding-top: 0;
    padding-bottom: 4px;
    line-height: 1.4;
  }

  .modal-footer {
    position: relative;
    padding: 10px 20px 20px;
    border-top: none;
    .btn {
      position: relative;
      padding-left: 16px;
      padding-right: 16px;
      z-index: 1;
    }
  }

  &.st-pos-coord {
    .modal-dialog {
      margin: 0;
    }
  }

  &[data-arr="left"],
  &[data-arr="topRight"],
  &[data-arr="topLeft"] {
    .modal-content {
      &:after {
        content: '';
        position: absolute;
        border-width: 5px;
        border-style: solid;
      }
    }
  }
  &[data-arr="left"] {
    .modal-content {
      &:after {
        top: calc(50% - 5px);
        left: -10px;
        border-color: transparent #FFF transparent  transparent;
      }
    }
  }
  &[data-arr="topRight"] {
    .modal-content {
      &:after {
        top: -10px;
        right: 10px;
        border-color: transparent transparent #FFF transparent;
      }
    }
  }

  &#modalTourSidebar {
    .modal-dialog {
      position: absolute;
      top: 50%;
      transform: translate(73px, -50%);
    }
  }

  &#modalTourStructure {
    .modal-dialog {
      position: absolute;
      top: 55px;
      left: 352px;
    }
  }

  &#modalTourSurvey {
    .modal-dialog {
      position: absolute;
      top: 68px;
      right: calc(100% - 1440px + 65px);
    }
  }


  @include media-breakpoint-down(lg) {
    &#modalTourSurvey {
      .modal-dialog {
        position: absolute;
        top: 75px;
        right: 65px;
      }
    }
  }

  @include media-breakpoint-down(md) {
    &#modalTourSidebar {
      .modal-dialog {
        top: 69px;
        left: 66px;
        right: 14px;
        transform: none;
      }
      .modal-content {
        &:after {
          top: -10px;
          left: 10px;
          transform: rotate(90deg);
        }
      }
    }

    &#modalTourStructure {
      .modal-dialog {
        top: 127px;
        left: 67px;
        right: 14px;
        transform: none;
      }
      .modal-content {
        &:after {
          top: -10px;
          left: 10px;
          transform: rotate(90deg);
        }
      }
    }

    &#modalTourSurvey {
      .modal-dialog {
        top: 127px;
        left: auto;
        right: 50px;
        transform: none;
      }
      .modal-content {
        &:after {
          top: -10px;
        }
      }
    }
  }

  @include media-breakpoint-down(sm) {
    &__body {
      display: block;
      padding: 28px 20px;
    }
    &__img {
      display: block;
      margin-left: auto;
      margin-right: auto;
    }
    &__main {
      width: 100%;
      margin-top: 40px;
      padding-left: 0;
    }


    .modal-header {
      padding-top: 16px;
      padding-left: 16px;
      .close {
        top: 16px;
        right: 23px;
      }
    }
    .modal-body {
      padding-right: 16px;
      padding-left: 16px;
      line-height: 1.4;
    }

    .modal-footer {
      padding: 10px 16px 16px;

      .btn {
        padding: 6px;
      }
    }
  }
}
