.b-modal-menu {
  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 13px 16px 13px 20px;
    .close {
      opacity: 1;
      stroke: #EDEEF0;
    }
  }
  .b-sidebar-nav {
    display: block;
  }
  .modal-dialog {
    margin: 0;
    width: 320px;
    height: 100%;
  }
  .modal-content {
    height: 100vh;
    border-radius: 0;
    background: #262626;
  }
  .modal-body {
    height: calc(100% - 60px);
    padding: 12px 8px;
    overflow-y: auto;
  }

  &.fade .modal-dialog {
    transform: translate(-320px, 0);
  }
  &.show .modal-dialog {
    transform: none;
  }
}
