.b-participants {
  position: absolute;
  top: 0;
  left: 100%;
  width: calc((100vw - 798px - 134px) / 2);
  max-width: 320px;
  border-radius: 8px;
  border: 1px solid var(--grays-100, #EDEEF0);
  background: var(--White, #FFF);
  overflow: hidden;

  &__header {
    max-width: 212px;
    padding: 12px 8px 20px;

    .custom-control-label {
      padding-left: 11px;
      font-size: 12px;
      font-weight: 500;
      line-height: 125%;
    }
  }

  &__table {
    max-height: 530px;

    thead {
      position: sticky;
      top: -1px;
    }
  }

  .table {
    border-top: 1px solid var(--grays-100, #EDEEF0);
  }


  @include media-breakpoint-down(lg) {
    position: static;
    width: auto;
    margin-bottom: 16px;
    .ui-table-sort tr:last-child td {
      border-bottom: none;
    }
    &__table {
      max-height: 230px;
    }
  }
}
