.b-sidebar {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  width: 64px;
  flex: 0 0 auto;
  padding: 16px 12px;
  background-color: #262626;
  z-index: 10;
  transition: all .2s;
  overflow-x: hidden;


  &.st-ticky {
    position: fixed;
    top: auto;
    bottom: 0;
  }

  &__logo {
    display: inline-flex;
    align-items: center;
    padding: 4px;
    img {
      width: 32px;
      height: 32px;
    }
  }
  &__logo-name {
    width: 0;
    color: #FFF;
    font-family: Gilroy;
    font-size: 19px;
    font-weight: 700;
    line-height: 120%;
    opacity: 0;
    overflow: hidden;
    transition: opacity .4s;
  }
  &__btn-show {
    display: flex;
    justify-content: flex-end;
    height: 36px;
    margin-bottom: 8px;
    padding: 8px 0;
    border: none;
    border-top: 1px solid #333;
    border-bottom: 1px solid #333;
    background-color: transparent;
    fill: #fff;
    opacity: 1;
    transition: all .3s;
    &:hover {
      background: #383838;
      border-radius: 8px;
    }

    &.fixed {
      height: 0;
      margin-bottom: 0;
      padding: 0;
      opacity: 0;
      overflow: hidden;
    }
  }
  &__btn-hidden {
    width: 0;
    height: 100%;
    padding: 0;
    fill: #fff;
    border: none;
    background-color: transparent;
    opacity: 0;
    transition: all .3s;

    .ic {
      transform: rotate(180deg);
    }
    &.fixed {
      width: 20px;
      opacity: 1;
      overflow: hidden;
    }
  }
  &__top {
    flex-grow: 1;
  }
  &__trigger {
    display: none;
    width: 40px;
    height: 40px;
    align-items: center;
    justify-content: center;
    padding: 0;
    border: none;
    background-color: transparent;
    stroke: #FFF;
  }
  &__group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
  }
  &__group2 {
    display: flex;
    flex-grow: 1;
  }
  &__login {
    display: flex;
    align-items: center;
    justify-content: center;
    &_mob {
      display: none;
    }
  }
  &__login-name {
    display: flex;
    width: 32px;
    height: 32px;
    flex: 0 0 auto;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: #BF6AD4;
    color: #FFF;
    font-weight: 500;
    font-size: 14px;
    line-height: 1;
    &:after {
      content: none;
    }
  }
  &__login-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 54px;
  }
  &__language {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    .ui-language {
      display: flex;
      align-items: center;
      justify-content: center;
      min-width: 40px;
      height: 40px;
      border-radius: 8px;
      border: 1px solid rgba(51,51,51,.9);
      &:not(.ui-language_w-bdr) {
        width: 100%;
      }
      .filter-option-inner {
        text-align: center;
      }
      .dropdown {
        align-items: center;
      }
      .dropdown-toggle {
        padding: 0;
        font-size: 0;
        border: 1px solid #303030;
        border-radius: 4px;

        &:hover {
          background-color: #383838;
        }
        &::after {
          content: none;
          border-top-color: #686A6E;
        }
        .ic {
          width: 16px;
          height: auto;
          margin-right: 0;
        }

        &:not(.ui-select_w-border) {
          padding: 0;
        }
      }
      .dropdown.dropup {
        .dropdown-toggle {
          &::after {
            border-bottom-color: #686A6E;
          }
        }
      }
    }
  }

  @include media-breakpoint-up(lg) {
    &.fixed {
      width: 280px;

      .b-sidebar {
        &__logo-name {
          display: block;
          width: auto;
          margin-left: 12px;
          opacity: 1;
          overflow: visible;
        }
        &__btn-fix {
          display: block;
        }
        &__login {
          width: auto;
        }

        &__language {
          width: 100%;
          .ui-language {
            .dropdown {
              width: 100%;
            }
            .dropdown-toggle {
              width: 100%;
              height: auto;
              padding: 9px 12px;
              font-size: 14px;
              font-weight: normal;
              color: #FFF;
              &::after {
                content: '';
              }
            }
            .filter-option {
              display: flex;
              align-items: center;
            }
            .ic {
              margin-right: 12px;
            }
          }
        }
      }

      .b-sidebar-nav {
        &__name {
          height: auto;
          opacity: 1;
        }
        &__subnav {
          &.show {
            display: block;
          }
        }
        &__link {
          &[aria-expanded="false"],
          &[aria-expanded="true"] {
            &:after {
              display: block;
            }
          }
        }
      }
    }
  }

  @include media-breakpoint-down(md) {
    position: sticky;
    top: 0;
    width: 100%;
    height: auto;
    min-height: 0;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 6px;
    overflow: visible;
    &__group {
      display: flex;
      align-items: center;
      margin-bottom: 0;
    }
    &__group2 {
      justify-content: flex-end;
      align-items: center;
    }
    &__btn-show {
      display: none;
    }
    &__language {
      flex-grow: 0;
    }
    &__top {
      display: inline-flex;
      align-items: center;
    }
    &__trigger {
      display: inline-flex;
      margin-left: 2px;
    }
    &__logo {
      margin-bottom: 0;
      padding: 7px 6px;
    }
    &__login {
      width: 54px;
      height: 54px;
      &_mob {
        display: inline-flex;
      }

      .b-sidebar-nav {
        .b-sidebar-nav__subnav-list {
          padding-top: 8px;
          padding-bottom: 1px;
          padding-left: 6px;
          border-radius: 8px;
        }
        .b-sidebar-nav__login-name {
          margin-left: 0;
        }
      }
    }
    &__login-name {
      border: none;
    }
  }
}


.b-sidebar-nav {
  display: flex;
  flex-grow: 1000;
  justify-content: center;
  &__list {
    width: 100%;
    flex-direction: column;
    margin-bottom: 0;
  }
  &__name {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    height: 40px;
    width: 216px;
    flex: 0 0 auto;
    transition: opacity .5s;
    color: var(--White, #FFF);
    font-size: 14px;
    line-height: 132%;
    opacity: 0;
  }
  &__subnav-list {
    list-style-type: none;
  }
  &__link {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    padding: 0;
    border: none;
    background-color: transparent;
    border-radius: 8px;
    &:after {
      content: '';
      position: absolute;
      display: none;
      right: 0;
      top: 0;
      width: 40px;
      height: 40px;
      background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="none"><g opacity="0.3"><path d="M13 5.5L8 10.5L3 5.5" stroke="%23F5F7FA" stroke-linecap="round" stroke-linejoin="round"/></g></svg>') no-repeat 50%;
    }
    &[aria-expanded="true"] {
      &:after {
        transform: rotate(180deg);
      }
    }

    &:hover,
    &.active {
      text-decoration: none;
      background-color: #383838;
    }
    &.active {
      pointer-events: none;
    }
    .badge {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 1;
    }
  }
  &__sublink {
    &.active {
      color: #FFF;
      pointer-events: none;
    }
  }
  &__subnav {
    padding-left: 2px;
    &.show {
      display: none;
    }
    a {
      display: block;
      padding: 6px;
      color: var(--grays-300, #B5BBC2);
      font-size: 14px;
      line-height: 140%;
      text-decoration: none;
      
      &:hover {
        color: #FFF;
        background: #383838;
        border-radius: 6px;
      }
    }
  }
  & &__subnav-list {
    padding: 6px;
    border-radius: 8px;
    border: 1px solid #262626;
    background: #191919;
    li {
      height: auto;
      -webkit-justify-content: flex-start;
              justify-content: flex-start;
      &:not(:last-child) {
        margin-bottom: 2px;
      }
    }
  }

  ul {
    margin-bottom: 0;
  }
  li {
    width: 100%;
    &.st-devider {
      flex-grow: 1;
      height: auto;
      max-height: 100%;
      margin-bottom: 8px;
      padding-top: 8px;
      border-bottom: 1px solid#333;
    }

    &.b-sidebar-nav__login {
      a {
        opacity: 1;
      }
    }
  }
  .ic {
    &_active {
      display: none;
    }
  }
  &__btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 0 0 auto;
    width: 40px;
    height: 40px;
    padding: 0;
    border: none;
    background-color: transparent;
    border-radius: 8px;
    z-index: 1;

    .ic_norm {
      stroke: #fff;
      fill: none;
    }
    .ic {
      fill: #fff;
    }
  }
  &__login {
    min-height: 54px;
    margin-top: 8px;
    margin-bottom: 12px;
    padding-top: 8px;
    border-top: 1px solid#333;
    .b-sidebar-nav__link {
      opacity: 1;
    }
    .b-sidebar-nav__subnav {
      top: auto;
      bottom: -40px;
    }
  }

  &__login-nav {
    display: flex;
    align-items: center;
    min-width: 226px;
    padding: 6px 12px;
    text-align: left;
  }
  &__login-group {
    width: calc(100% - 34px);
  }
  &__login-name {
    margin-left: 10px;
    color: var(--grays-200, #DADDE1);
    font-size: 14px;
    line-height: 140%;
  }
  &__login-info {
    display: block;
    width: 100%;
    color: var(--grays-300, #B5BBC2);
    font-size: 12px;
    line-height: 125%;
  }
  &__divider {
    width: 100%;
    margin-top: 4px !important;
    margin-bottom: 4px;
    border-bottom: 1px solid #242424;
  }



  @include media-breakpoint-down(md) {
    display: none;
    &.dropdown-menu {
      border: none;
      border-radius: 10px;
    }
    &__btn {
      display: inline-block;
      width: auto;
      height: auto;
      margin-right: 16px;
      color: #FFF;

      .ic {
        width: 24px;
        height: 24px;
      }
    }
    &__list {
      padding-left: 0;
    }
    &__link,
    &__trigger {
      position: relative;
      display: flex;
      align-items: center;
      width: 100%;
      height: auto;
      padding: 12px;
      line-height: 1.25;
      color: #FFF;
      border: none;
      border-radius: 8px;
      background-color: transparent;
    }
    &__name {
      display: block;
      height: auto;
      opacity: 1;
      transform: none;
    }
    &__collapse {
      padding-left: 40px;
      padding-bottom: 8px;
      background-color: #191919;
      .b-sidebar-nav__link {
        padding: 7px 13px;
        line-height: 1.4;
        color: #9198A4;
        &.active {
          font-weight: 500;
          color: #fff;
        }
      }
      .b-sidebar-nav__collapse {
        margin-bottom: 6px;
        padding-left: 16px;
        .b-sidebar-nav__link {
          font-size: 13px;
        }
      }
    }
    &__subnav {
      position: static;
      opacity: 1;
      display: block;
      padding: 0;
      &.show {
        display: block;
      }
    }
    &__link {
      opacity: 1;
      &[aria-expanded="false"],
      &[aria-expanded="true"] {
        &:after {
          display: block;
          top: 3px;
          background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="none"><path d="M13 5.5L8 10.5L3 5.5" stroke="%23fff" stroke-linecap="round" stroke-linejoin="round"/></svg>') no-repeat 50%;
        }
      }
      &[aria-expanded="true"] {
        background-color: #191919;
        border-radius: 8px 8px 0 0;
      }
      &.active {
        background: #191919;
      }
      .badge {
        top: -8px;
        right: -8px;
      }
    }
    li {
      display: block;
      height: auto;
      margin-bottom: 4px;
      border-radius: 8px;
      &.active {
        padding-bottom: 6px;
        background-color: #191919;
      }
      a {
        display: flex;
      }
    }
    .b-sidebar-nav__subnav-list {
      padding-top: 0;
      padding-bottom: 8px;
      padding-left: 48px;
      border: none;
      border-radius: 0 0 8px 8px;
    }
    .b-sidebar-nav__login-nav {
      display: flex;
    }
  }
}
