.b-rating-main {
  display: flex;
  height: 100%;
  padding: 16px 20px;
  border-radius: 6px;
  background: var(--Main-green-50, #F3F7F3);
  color: var(--Secondary, #686A6E);
  font-size: 11px;
  line-height: 125%;
  &__title {
    padding-top: 24px;
    padding-right: 16px;
    color: var(--Dark, #111722);
  }
  &__main {
    text-align: center;
  }
  &__blocks {
    display: flex;
    margin-top: 5px;
  }
  &__group {
    text-align: left;
  }
  &__row {
    display: flex;
  }
  &__item {
    width: 40px;
    margin-right: 6px;
    padding: 8px;
    border-radius: 2px;
    background: #FACBC6;
    text-align: center;
    color: var(--Dark, #111722);
    font-size: 12px;
    font-weight: 600;
    line-height: normal;
    &.st-neutral {
      background: #FCF1CC;
    }
    &.st-positive {
      background: #B7DEC3;
    }
  }
  &__total {
    display: block;
    color: var(--Secondary, #686A6E);
  }
  &__number {
    font-size: 18px;
    font-weight: 700;
    line-height: normal;
  }
}

.b-rating-second {
  height: 100%;
  padding: 16px;
  border-radius: 6px;
  background: #FFEFEE;
  color: var(--Secondary, #686A6E);
  font-size: 12px;
  line-height: 125%;
  &__title {
    color: var(--Dark, #111722);
    font-size: 18px;
    font-weight: 700;
    line-height: normal;
  }
  &__group {
    display: flex;
    align-items: center;
    padding-top: 16px;
    font-size: 13px;
  }
  &__item {
    margin-right: 6px;
    line-height: 1.4;
  }
  &__number {
    font-size: 24px;
    font-weight: 700;
    line-height: 130%;
  }

  @include media-breakpoint-down(sm) {
    &__title {
      display: block;
    }
  }
}


.b-rating-index {
  margin-top: 4px;
  &__group {
    display: flex;
    margin-top: 8px;
    border-radius: 6px;
    border: 1px solid var(--Grays-100, #EDEEF0);
  }
  &__item {
    position: relative;
    width: 33.33%;
    padding: 16px 16px 20px;
    line-height: 125%;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 16px;
      right: 16px;
      height: 2px;
    }
    &.st-negative {
      &:after {
        background-color: var(--Red, #E83137);
      }
    }
    &.st-neutral {
      &:after {
        background-color: var(--Smiley-Yellow, #FFCE00);
      }
    }
    &.st-positive {
      &:after {
        background-color: var(--Smiley-Green, #7ED321);
      }
    }
    &:not(.active) {
      opacity: 0.4;
      &:after {
        content: none;
      }
      .b-rating-index__number {
        color: var(--Dark, #111722);
      }
    }
  }
  &__title {
    color: var(--Dark, #111722);
    font-size: 14px;
    font-weight: 500;
  }
  &__number {
    margin-top: 2px;
    margin-bottom: 8px;
    font-size: 20px;
    font-weight: 700;
    line-height: normal;
  }
  &__info {
    color: #000;
    font-size: 12px;
  }


  @include media-breakpoint-down(sm) {
    &__group {
      display: block;
    }
    &__item {
      width: 100%;
      &:not(:last-child) {
        padding-bottom: 12px;
      }
    }
  }
}
