.b-chart {
  margin-bottom: 24px;
  border-radius: 6px;
  border: 1px solid var(--grays-200, #DADDE1);
  background: var(--White, #FFF);
  line-height: 140%;
  font-size: 14px;
  overflow: hidden;

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 20px;
    border-bottom: 1px solid var(--grays-100, #EDEEF0);
  }
  &__header-title {
    color: var(--Dark, #111722);
    font-size: 16px;
    font-weight: 500;
  }
  &__header-info {
    color: var(--grays-600, #475467);
    font-size: 13px;
  }
  &__header-btns {
    white-space: nowrap;
  }
  &__header-btn {
    margin-left: 16px;
    stroke: #9198A4;

    &.type-notific {
      fill: #fff;
    }

    .ic {
      width: 20px;
      height: 20px;
    }

    &.active,
    &:hover {
      stroke: #2F8733;
      &.type-notific {
        fill: #2F8733;
      }
    }
  }
  &__main {
    padding: 20px;
    display: flex;
    justify-content: center;
  }
  &__info {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 12px 20px;
    color: var(--grays-900, #1C2229);
  }
  &__info-item {
    margin-right: 16px;
    padding-right: 16px;
    border-right: 1px solid var(--grays-100, #EDEEF0);
  }
  &__info-number {
    margin-left: 12px;
    color: var(--main-green-600, #2F8733);
    text-align: center;
    font-weight: 500;
  }
  &__footer {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px 12px 12px 16px;
    background: var(--grays-25, #F9FAFB);
    color: var(--grays-700, #394352);
    font-weight: 500;
  }
  &__footer-logo {
    margin-right: 16px;
  }
  &__footer-btn {
    margin-left: 24px;
  }

  &__descrip {
    display: flex;
    align-items: center;
    color: var(--grays-1000, #0E1115);
    font-size: 16px;
    font-weight: 500;
    line-height: 140%;
  }
  &__descrip-item {
    display: inline-flex;
    align-items: center;
    color: var(--grays-1000, #0E1115);
    font-size: 16px;
    font-weight: 500;
    line-height: 140%;
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }
  &__descrip-spinner {
    position: relative;
    display: flex;
    .ui-spinner {
      &:after {
        display: none;
      }
    }
  }
  &__descrip-sp-group {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }
  &__descrip-total {
    text-align: center;
    font-size: 24px;
    font-weight: 600;
    line-height: 110%;
  }

  &__descrip-counter {
    display: flex;
    align-items: center;
    max-width: 431px;
    margin-left: 120px;
    padding: 15px 40px 15px 0;
    border-radius: 10px;
    background: var(--grays-50, #F5F7FA);
    color: var(--grays-1000, #0E1115);
  }
  &__descrip-info {
    font-size: 14px;
  }
  &__settings {
    padding: 20px;
    background: var(--Grays-50, #F5F7FA);

    .ui-select {
      background-color: #FFF;
    }
  }


  @include media-breakpoint-down(md) {
    &__descrip {
      display: block;
    }
    &__descrip-main {
      margin-bottom: 24px;
      text-align: center;
    }
  }


  @include media-breakpoint-down(sm) {
    &__header {
      flex-direction: column-reverse;
    }
    &__header-btns {
      margin-left: auto;
      margin-bottom: 10px;
    }
    &__footer {
      flex-wrap: wrap;
      justify-content: flex-start;
    }
    &__footer-group {
      display: flex;
      width: 100%;
      margin-bottom: 10px;
    }
    &__footer-btn {
      margin-left: 0;
    }
    &__descrip-counter {
      max-width: 100%;
      margin-left: 0;
      flex-direction: column;
      padding-right: 0;
      text-align: center;
    }
    &__descrip-info {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
}
