.b-email-template {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 32px;
  background: #F5F5F5;
  &__inner {
    width: 634px;
    background: #FFFFFF;
    border: 1px solid #EDEEF0;
    border-radius: 8px;
    padding: 20px 32px;
  }
  &__header {
    padding: 12px 0px;
    text-align: left;
  }
  &__title {
    padding-top: 32px;
    padding-bottom: 28px;
    font-weight: 600;
    font-size: 24px;
    line-height: 28px;
  }
  &__content {
    font-size: 16px;
    line-height: 1.5;
    color: #475467;
    p {
      margin-bottom: 12px;
      text-indent: 22px;
    }
    a:not(.btn) {
      font-weight: 500;
      text-decoration: underline;
    }
  }
  &__btn {
    margin-top: 15px;
    padding: 14px 28px;
    font-size: 16px;
    font-weight: 600;
    border-radius: 7px;
  }
  &__block {
    margin-top: 16px;
    padding: 16px 32px;
    font-size: 15px;
    p {
      margin-bottom: 3px;
      text-indent: 0;
    }
    ul {
      padding-left: 25px;
    }
  }
  &__info {
    margin-top: 28px;
    margin-bottom: 15px;
    padding: 32px 16px 32px 16px;
    color: #475467;
    background-color: #F9FAFB;
    border-radius: 8px;
    text-align: center;
    p {
      margin-bottom: 0;
      font-size: 15px;
      text-indent: 0;
    }

    strong {
      display: block;
      margin-bottom: 4px;
      font-weight: 600;
      color: #1C2229;
      font-size: 16px;
      line-height: 22px;
    }
  }
  &__footer {
    position: relative;
    margin-top: 40px;
    padding: 0 30px 24px;
    font-size: 14px;
    line-height: 20px;
    color: rgba(51, 51, 51, 0.5);
    text-align: center;
    &:after {
      content: '';
      display: block;
      position: absolute;
      top: -18px;
      left: 50%;
      width: calc(100% - 147px);
      transform: translateX(-50%);
      border-top: 1px solid rgba(0, 0, 0, 0.2);
    }
  }
  &__copyright {
    margin-top: 14px;
    font-size: 12px;
    line-height: 16px;
    color: #999999;
  }
}
