.b-reviews {
  max-height: 100vh;

  &__content {
    padding: 28px 24px;
    background-color: #FFF;

    // .b-outside__inner {
    //   max-width: 768px;
    // }
  }

  &__search {
    max-width: 320px;
    margin-bottom: 12px;
  }

  &__group {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
    border-radius: 6px;
    border: 1px solid var(--grays-100, #EDEEF0);
    background: var(--White, #FFF);
    overflow: hidden;
  }

  &__cloud {
    padding: 24px;
    width: calc(100% - 336px);
    flex: 0 0 auto;
  }
  &__cloud-title {
    margin-bottom: 24px;
    color: var(--grays-900, #1C2229);
    font-size: 16px;
    font-weight: 500;
    line-height: 140%;
  }
  &__cloud-main {
    width: 100%;
    height: 100%;
  }
  &__cards {
    width: 336px;
    flex: 0 0 auto;
    padding: 24px;
    background: var(--grays-25, #F9FAFB);
  }


  @include media-breakpoint-down(md) {
    height: calc(100vh - 54px);
  }

  @include media-breakpoint-down(sm) {
    &__group {
      display: block;
    }
    &__cloud {
      width: 100%;
    }
    &__cards {
      width: 100%;
      padding: 20px;
    }
    &__content {
      padding-left: 12px;
      padding-right: 12px;
    }
    &__cloud-main {
      height: 330px;
    }
  }
}

.b-reviews-nav {
  display: flex;
  background: var(--grays-100, #EDEEF0);
  border-bottom: 1px solid var(--grays-100, #EDEEF0);

  &__item {
    position: relative;
    width: 348px;
    padding: 16px 24px;
    border-right: 1px solid var(--grays-200, #DADDE1);

    &:after {
      content: '';
      position: absolute;
      top: -4px;
      left: 0;
      right: 0;
      height: 4px;
      background: var(--Main, #2F8733);
      opacity: 0;
      transition: all .3s;
    }
    &.active {
      background-color: #FFF;
      text-decoration: none;
      pointer-events: none;
      &:after {
        opacity: 1;
      }
      .b-reviews-nav__title {
        color: var(--main-green-600, #2F8733);
      }
    }
    &:hover {
      border-right: 1px solid var(--grays-200, #DADDE1);
      background: var(--grays-50, #F5F7FA);
      text-decoration: none;
      &:after {
        opacity: 1;
      }
    }
  }
  &__title {
    margin-bottom: 2px;
    color: var(--grays-600, #475467);
    font-size: 16px;
    font-weight: 500;
    line-height: 140%;
  }
  &__text {
    color: var(--grays-500, #6C7685);
    font-size: 14px;
    line-height: 140%;
  }

  &_mod-a {
    flex-wrap: wrap;
    padding-left: 24px;
    padding-right: 24px;
    border-bottom: 1px solid var(--grays-200, #DADDE1);
  }
  &_mod-a & {
    &__item {
      width: 332px;
      box-shadow: -1px 1px 0 var(--grays-200, #DADDE1);
      &.active,
      &:hover {
        &:after {
          display: none;
        }
      }
      &.active{
        // border-color: #2F8733;
        box-shadow: 0px 0px 0 1px #2f8733;
        z-index: 1;
      }
    }
  }


  @include media-breakpoint-down(lg) {
    &__item {
      width: auto;
      max-width: 348px;
    }
  }

  @include media-breakpoint-down(sm) {
    flex-wrap: nowrap;
    overflow-y: none;
    overflow-x: auto;
    &__item {
      width: 348px;
      flex: 0 0 auto;
    }
  }
}


.b-reviews-filter {
  padding: 20px 24px 20px;
  background-color: #FFF;
  border-bottom: 1px solid var(--grays-100, #EDEEF0);
  &__title {
    margin-bottom: 2px;
    color: var(--grays-900, #1C2229);
    font-size: 16px;
    font-weight: 500;
    line-height: 140%;
  }
  &__text {
    margin-bottom: 12px;
    color: var(--grays-500, #6C7685);
    font-size: 14px;
    line-height: 140%;
  }
  &__group {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  &__item {
    margin-bottom: 8px;
    &:not(:last-child) {
      margin-right: 8px;
    }
    .ui-select {
      background: #FFF;
    }
  }
  &__btn {
    &.active {
      border: 1px solid var(--Main, #2F8733);
      background: var(--main-green-100, #EAF3EB);
      color: var(--Main, #2F8733);
      stroke: var(--Main, #2F8733);
    }
  }
  &__badge {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 18px;
    height: 18px;
    margin-left: 8px;
    padding: 0 2px;
    border-radius: 4px;
    background: var(--main-green-600, #2F8733);
    color: var(--base-white, #FFF);
    font-size: 12px;
    font-weight: 600;
    line-height: 1;
  }

  @include media-breakpoint-down(sm) {
    &__group {
      // display: none;
      &.show {
        display: flex;
      }
    }
  }
}



.b-reviews-chart {
  border-radius: 6px;
  border: 1px solid var(--grays-100, #EDEEF0);
  background: var(--White, #FFF);
  + .b-reviews-chart {
    margin-top: 24px;
  }
  &__card {
    height: 100%;
    padding: 20px;
    border-radius: 6px;
    background: var(--White, #FFF);
    + .b-reviews-chart__card {
      margin-top: 24px;
    }
  }
  &__group {
    padding: 24px;
    background: var(--grays-25, #F9FAFB);
  }
  &__title {
    margin-bottom: 14px;
    color: var(--Dark, #111722);
    font-size: 15px;
    font-weight: 500;
    line-height: 144%;
  }
  &__table {
    width: 100%;
    color: var(--grays-800, #2B323E);
    font-size: 14px;
    line-height: 140%;
    td {
      vertical-align: top;
      &:last-child {
        padding-left: 20px;
        text-align: right;
        color: var(--grays-800, #2B323E);
        font-size: 16px;
        font-weight: 500;
      }
    }
    tr {
      &:not(:last-child) {
        td {
          padding-bottom: 12px;
        }
      }
    }
  }
  &__table-total {
    font-size: 20px;
    font-weight: 700;
    line-height: 1.2;
  }
  &__table-note {
    display: block;
    color: var(--grays-800, #2B323E);
    font-size: 12px;
    font-weight: 400;
    line-height: 125%;
    white-space: nowrap;
  }

  @include media-breakpoint-down(md) {
    &__card {
      height: auto;
      &_next {
        margin-top: 24px;
      }
    }
  }
}


.b-reviews-dynamics {
  line-height: 140%;
  th {
    padding-bottom: 16px;
    color: var(--grays-900, #1C2229);
    font-size: 14px;
    font-weight: 400;
  }
  td {
    vertical-align: bottom;
    white-space: nowrap;
    &:not(:last-child) {
      padding-right: 56px;
    }
    .ic {
      width: 28px;
      height: 28px;
    }
  }
  tr {
    &:not(:last-child) {
      td {
        padding-bottom: 20px;
      }
    }
  }
  &__number {
    display: inline-block;
    margin-left: 8px;
    margin-right: 5px;
    font-size: 20px;
    font-weight: 700;
  }
  &__note {
    font-size: 12px;
    line-height: 1.2;
  }
  &__inner {
    display: inline-flex;
    align-items: flex-end;
  }
  .ui-st-arr {
    &.ui-st-down {
      &:before {
        background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="10" height="10" viewBox="0 0 10 10" fill="none"><path d="M1.875 8.125L8.125 1.875M8.125 1.875L3.4375 1.875M8.125 1.875V6.5625" stroke="%23EA4435" stroke-width="1.25" stroke-linecap="round" stroke-linejoin="round"/></svg>');
      }
    }
  }


  @include media-breakpoint-down(sm) {
    td {
      &:not(:last-child) {
        padding-right: 24px;
      }
    }
  }
}

.b-reviews-methods {
  max-width: 657px;
}



.b-reviews-switch {
  display: flex;
  align-items: center;
  max-width: 620px;

  .custom-control-label {
    padding-left: 20px;
    line-height: 140%;
  }
  &__text {
    display: block;
    margin-bottom: 2px;
    font-size: 16px;
    font-weight: 500;
  }
  &__title {
    display: block;
    font-size: 14px;
  }
  .custom-switch {
    .custom-control-label {
      &::before {
        top: calc(50% - 10px);
      }
      &::after {
        top: calc(50% - 8px);
      }
    }
  }
}
