.b-control {
  background-color: #F5F5F5;

  &_qr {
    .b-control-header {
      padding-top: 10px;
      padding-bottom: 10px;
      background-color: #FAFAFA;

      @include media-breakpoint-down(sm) {
        padding-right: 12px;
        &__btn-w-ic {
          font-size: 0;
          .ic {
            margin-right: 0;
          }
        }
      }
    }

    .b-control-main {
      height: calc(100vh - 121px);

      @include media-breakpoint-down(lg) {
        width: calc(100vw - 64px);
      }

      @include media-breakpoint-down(md) {
        height: calc(100vh - 244px);
        width: auto;
      }
    }

    .b-control-sidebar {
      &__main {
        height: 100%;
      }
    }

    .b-control-options {
      &__title {
        font-size: 16px;
        + .form-group {
          margin-top: 16px;
        }
      }
      &__item {
        margin-bottom: 0;
        padding: 16px 12px 20px;
        border-radius: 0;
        border-width: 0 0 1px;
        &:last-child {
          border-bottom: none;
        }
      }
    }

  }
}

.b-control-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 24px;
  border-bottom: 1px solid #EDEEF0;

  &__switch {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 154px;
    height: 48px;
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    color: #2B323E;
    border-right: 1px solid #EDEEF0;
    &:hover {
      text-decoration: none;
    }
  }
  &__switch-btn {
    position: relative;
    margin: 0 12px;
    width: 36px;
    height: 20px;
    background: #DADDE1;
    border-radius: 999px;
    &.active {
      background: #2F8733;
    }
    &:after {
      content: '';
      position: absolute;
      width: 16px;
      height: 16px;
      left: 2px;
      top: calc(50% - 16px/2);
      background: #FFFFFF;
      box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.04), 0px 1px 3px rgba(0, 0, 0, 0.06);
      border-radius: 999px;
    }
  }
  &__btns {
    display: inline-flex;
    align-items: center;
    // padding-top: 4px;
    // padding-bottom: 4px;
    .btn {
      margin-left: 12px;
    }
  }
  &__btn {
    min-width: 110px;
  }
  &__views {
    display: inline-flex;
    align-items: center;
    margin-left: 12px;
    margin-right: 20px;
    .btn {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 40px;
      padding: 0;
      fill: #475467;
      border-radius: 3px;
      &.active {
        background: #F3F7F3;
        fill: #2F8733;
      }
    }
  }
  &__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    letter-spacing: 0.002em;
    color: #1C2229;
  }
  &__link {
    display: flex;
    width: 40px;
    height: 40px;
    align-items: center;
    justify-content: center;
    flex: 0 0 auto;
    margin-right: 16px;
    .ic {
      stroke: #6C7685;
      transition: all .3s;
    }

    &:hover {
      .ic {
        stroke: var(--main-green-600, #2F8733);
      }
    }
  }
  &__group {
    display: flex;
    align-items: center;
    margin-left: 16px;
    white-space: nowrap;
    overflow-x: auto;
  }

  @include media-breakpoint-down(md) {
    &__views {
      display: none;
    }
  }

  @include media-breakpoint-down(md) {
    &__link {
      margin-right: 4px;
    }
    &__group {
      margin-left: 8px;
    }
  }
}


.b-control-main {
  display: flex;
  justify-content: space-between;
  height: calc(100vh - 109px);

  &_grid {
    display: grid;
    grid-template-columns: 420px 1fr;
  }

  @include media-breakpoint-down(md) {
    position: relative;
    display: block;
    height: calc(100vh - 232px);
    padding-top: 20px;
    padding-bottom: 20px;
    overflow-y: auto;
    &_grid {
      display: grid;
      grid-template-columns: 100% 100%;
      overflow-x: hidden;
      &.st-view {
        .b-control-row__sidebar {
          transform: translateX(calc(100% + 32px));
        }
        .b-control-row__frame {
          margin-left: 16px;
          margin-right: 16px;
          padding: 0;
          transform: translateX(calc(-100% - 32px));
        }
      }

      .b-control-row__frame {
        transform: translateX(100%);
      }
    }
  }
}

.b-control-row {
  &__sidebar {
    margin: 0 0 20px 16px;
    border-radius: 10px;
    border: 1px solid var(--Grays-200, #DADDE1);
    background: var(--White, #FFF);
    border-bottom: 1px solid #DADDE1;
    &:first-child + .b-control-row__frame {
      padding-top: 20px;
    }
  }
  &__title {
    max-width: 100vw;
    grid-column-start: 1;
    grid-column-end: 3;
    padding: 16px 16px 20px 16px;
    font-size: 20px;
    font-weight: 700;
    line-height: normal;
  }
  &__frame {
    margin-bottom: 20px;
    padding: 0 20px 0 24px;

    .b-frame {
      display: flex;
      flex-direction: column;
      min-height: 100%;

      &__body {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
      }
      &__content {
        flex-grow: 1;
      }
      &__item {
        flex-grow: 1;
      }
    }
  }

  @include media-breakpoint-down(md) {
    &__sidebar {
      margin-right: 16px;
    }
  }
}

.b-control-sidebar {
  width: 420px;
  flex: 0 0 auto;
  min-height: 100%;
  background-color: #FFF;
  &.b-control-row__sidebar {
    margin-top: 20px;
    min-height: auto;
  }
  &__add-face {
    display: flex;
    width: 68px;
    height: 68px;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    border: 1px solid var(--Main-green-600, #2F8733);
    background: var(--Main-green-100, #EAF3EB) url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"><path fill-rule="evenodd" clip-rule="evenodd" d="M12 3.75C12.4142 3.75 12.75 4.08579 12.75 4.5V11.25H19.5C19.9142 11.25 20.25 11.5858 20.25 12C20.25 12.4142 19.9142 12.75 19.5 12.75H12.75V19.5C12.75 19.9142 12.4142 20.25 12 20.25C11.5858 20.25 11.25 19.9142 11.25 19.5V12.75H4.5C4.08579 12.75 3.75 12.4142 3.75 12C3.75 11.5858 4.08579 11.25 4.5 11.25H11.25V4.5C11.25 4.08579 11.5858 3.75 12 3.75Z" fill="%232F8733"/></svg>') 50% 50% no-repeat;
  }
  &__date {
    display: flex;
    justify-content: space-between;
    .ui-date-picker-wrap {
      background-color: #FFF;
      width: calc(50% - 4px);
    }
  }
  &__title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 12px 12px 16px;
    background: #FAFAFA;
    border-bottom: 1px solid #F5F7FA;
    font-weight: 500;
    font-size: 18px;
    line-height: 140%;
    color: #2B323E;
    &:first-child {
      border-radius: 10px 10px 0 0;
    }

    .h8 {
      line-height: 1.4;
    }
  }
  .b-control-sidebar__title {
    &.b-control-sidebar__title_mod-a {
      margin-top: 20px;
      padding-right: 24px;
    }
  }
  &__main {
    + .b-control-sidebar__title-wrap {
      padding-top: 36px;
    }
  }
  &__templates {
    padding: 16px 12px;
  }
  &__modal-select {
    padding: 16px;
    border-bottom: 1px solid #EDEEF0;
  }
  &__modal-btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 8px;
    .ic {
      margin-left: 8px;
    }
  }

  @include media-breakpoint-down(md) {
    // width: 100%;
    width: auto;
    margin-top: 0;
    margin-bottom: 0;
    height: 100%;
    &.b-control-row__sidebar {
      margin-top: 0;
    }
    &.st-less {
      position: absolute;
      top: 20px;
      left: 0;
      right: 0;
      bottom: 20px;
      height: auto;
      z-index: -1;
    }
    &.active {
      z-index: 1;
      opacity: 1;
    }
  }
}


.b-control-frame {
  padding: 20px;
  // margin: 20px 20px 0 24px;
  // padding-bottom: 20px;
  flex-grow: 1;
  min-height: 100%;

  &__width-620 {
    max-width: 620px;
    margin-left: auto;
    margin-right: auto;
  }

  &.ui-scroll {
    margin-top: 0;
    overflow-x: hidden;
  }

  &.mob-preview {
    flex: 0 0 auto;
    width: 383px;
    margin-left: auto;
    margin-right: auto;

    .b-frame {
      &__input-wrap {
        width: 100%;
      }
      &__input {
        width: 100%;
      }
      &__area-wrap {
        width: calc(100% - 28px);
        margin-left: 14px;
        margin-right: 14px;
      }
      &__lg-title {
        font-size: 18px;
      }
      &__content-wrap {
        width: 100%;
        padding-left: 24px;
        padding-right: 24px;
      }
    }
  }

  @include media-breakpoint-down(lg) {
    width: calc(100% - 436px);
    // width: calc(100% - 464px);
    flex: 0 0 auto;
    margin-left: auto;
    margin-right: auto;
  }

  @include media-breakpoint-down(md) {
    width: 100%;
    margin: 0;
    padding: 0 12px;
    // background-color: #EDEEF0;
    opacity: 0;
    overflow: hidden;
    &.st-less {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      z-index: -1;
    }
    &.active {
