    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #EDEEF0;
    padding: 16px 16px 24px;
    font-weight: 500;
    line-height: 125%;
    color: #1C2229;
  }
  &__switch {
    display: flex;
    margin-right: 12px;
