      + .b-control-question {
        margin-top: 28px;
      }

      &__add {
        margin-top: 12px;
        stroke: #6C7685;
      }
    }
  }
}

.b-control-question {
  margin-left: 8px;
  margin-right: 8px;
  border-radius: 8px;
  border: 1px solid #EDEEF0;
  overflow: hidden;

  + .b-control-question {
    margin-top: 68px;
  }
  &__header {
    display: flex;
    align-items: center;
    padding: 10px 6px;
    border-bottom: 1px solid #EDEEF0;
    background: #F9FAFB;
  }
  &__title {
    flex-grow: 1;
    color: #2B323E;
    font-size: 16px;
    font-weight: 500;
    line-height: 120%;
  }
  &__body {
    padding: 16px;
  }
  &__move {
    margin-right: 8px;
    border: none;
    background-color: transparent;
    cursor: grab !important;
    &:active {
      cursor: grabbing !important;
    }
  }
  &__del {
    border: none;
    background-color: transparent;

    .ic {
      stroke: #9198A4;
    }
  }
  &__add {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-top: 58px;
    color: #6C7685;
    font-size: 13px;
    line-height: 140%;
    border: none;
    background: #FFF;
    overflow: hidden;
    span {
      position: relative;
      z-index: 1;
      background: #FFF;
      padding: 3px 5px;
    }
    &:after {
      content: '';
      position: absolute;
      left: 10px;
      right: 10px;
      top: 12px;
      border-top: 1px dashed #6C7685;
    }
    .ic {
      margin-right: 6px;
    }
  }
}

.b-control-switch {
  &__item {
    display: flex;
    margin-bottom: 8px;
  }
  &__group {
    position: relative;
    flex-grow: 1;
    overflow: hidden;
    .form-control {
      padding-right: 48px;
    }
  }
  &__move {
    position: absolute;
    top: 1px;
    right: 1px;
    bottom: 1px;
    z-index: 1;
    display: flex;
    width: 36px;
    padding: 8px;
    justify-content: center;
    align-items: center;
    border-width: 0;
    border-left: 1px solid var(--Stroke, #D0D3D8);
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    background: var(--grays-50, #F5F7FA);
    cursor: grab !important;

    &:active {
      cursor: grabbing !important;
    }
  }
  &__del {
    display: flex;
    width: 36px;
    height: 36px;
    margin-left: 10px;
    padding: 4px;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    border: 1px solid var(--grays-200, #DADDE1);
    background-color: #FFF;
    .ic {
      stroke: #9198A4;
    }
  }
  &__btns {
    margin-top: 12px;
  }
}

.b-control-mob {
  @include media-breakpoint-down(md) {
    &__item-normal {
      display: none;
      height: auto !important;
      &.active {
        display: block;
      }
    }
  }
}
