.b-templates {
  display: flex;
  justify-content: space-between;
  padding-top: 12px;
  padding-right: 12px;
  &__col {
    flex: 0 0 auto;
    width: calc(50% - 14px);
    max-height: calc(100vh - 112px);
  }
  &__list {
    padding-right: 24px;
  }
  &__item {
    width: 100%;
    margin-bottom: 12px;

    &.disabled {
      .b-templates {
        &__info {
          color: #6C7685;
        }
        &__title {
          margin-right: 80px;
        }
        &__ic {
          fill: #6C7685;
        }
      }
    }
  }
  &__link {
    position: relative;
    display: flex;
    align-items: center;
    padding: 0;
    border: 1px solid #DADDE1;
    border-radius: 6px;
    transition: all .3s;
    &:hover {
      border-color: #2F8733;
    }
    &:not(.active) {
      .b-templates__title {
        padding-left: 0;
        padding-right: 0;
        color: #475467;
        background-color: transparent;
      }
    }
    &.active {
      border-color: #2F8733;
      box-shadow: 0 0 0 1px #2F8733;
      .b-templates {
        &__ic {
          fill: #2F8733;
        }
      }
    }
  }
  &__ic {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 0 0 auto;
    width: 78px;
    height: 100%;
    fill: #475467;
  }
  &__inner {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    flex: auto;
    width: calc(100% - 80px);
    min-height: 94px;
    padding: 24px;
    border-left: 1px solid #EDEEF0;
  }
  &__title {
    margin-bottom: 4px;
    font-weight: 600;
    font-size: 15px;
    line-height: 140%;
  }
  &__info {
    width: 100%;
    line-height: 125%;
    color: #475467;
  }
  &__footer {
    display: flex;
    justify-content: flex-end;
    padding: 16px 0;
    .btn {
      + .btn {
        margin-left: 8px;
      }
    }
  }
  &__footer-application {
    text-align: center;
    color: var(--grays-800, #2B323E);
    font-size: 15px;
    font-weight: 500;
    line-height: 144%;
    &.check {
      color: var(--grays-600, #475467);
      font-weight: 400;

      &:before {
        content: '';
        display: inline-block;
        width: 18px;
        height: 18px;
        margin-right: 5px;
        vertical-align: -12%;
        background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 18 18" fill="none"><g clip-path="url(%23clip0_4249_158051)"><path d="M9 16.5C13.125 16.5 16.5 13.125 16.5 9C16.5 4.875 13.125 1.5 9 1.5C4.875 1.5 1.5 4.875 1.5 9C1.5 13.125 4.875 16.5 9 16.5Z" stroke="%23475467" stroke-width="1.25" stroke-linecap="round" stroke-linejoin="round"/><path d="M5.8125 8.99997L7.935 11.1225L12.1875 6.8775" stroke="%23475467" stroke-width="1.25" stroke-linecap="round" stroke-linejoin="round"/></g><defs><clipPath id="clip0_4249_158051"><rect width="18" height="18" fill="white"/></clipPath></defs></svg>');
      }
    }
  }
  &__footer-link {
    padding: 0;
    border: none;
    background-color: transparent;
    text-decoration: underline;
    color: var(--main-green-600, #2F8733);
  }
  &__note {
    padding-left: 12px;
    color: #6C7685;
  }
  &__close {
    display: none;
  }

  @include media-breakpoint-only(md) {
    &__item {
      width: calc(33.33% - 12px);
      margin-right: 12px;
    }
    &__link {
      display: block;
      height: 100%;
    }
    &__inner {
      width: 100%;
    }
    &__ic {
      width: 100%;
      height: auto;
      justify-content: flex-start;
      padding-left: 16px;
      padding-top: 16px;
    }
  }


  @media (max-width: 1199.98px) and (min-width: 1024px) {
    &__footer {
      flex-wrap: wrap;
      .btn {
        width: 100%;
        + .btn {
          margin-left: 0;
          margin-top: 8px;
        }
      }
    }
  }

  @media (max-width: 1023.98px) {
    &__footer {
      .btn {
        width: 100%;
        + .btn {
          margin-left: 0;
        }
      }
    }
  }



  @include media-breakpoint-down(md) {
    display: block;
    padding-right: 0;
    padding-top: 0;
    &__col {
      width: 100%;
      max-height: 100%;
    }
    &__title {
      display: block;
      margin-bottom: 4px;
    }
    &__item.disabled .b-templates__title {
      margin-right: 0;
    }
    &__pane {
      position: absolute;
      top: 54px;
      left: 0;
      bottom: 0;
      right: 0;
      height: calc(100vh - 54px);
      background-color: #FFF;
      &.active {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
      }
    }
    &__footer {
      position: sticky;
      bottom: 0;
      padding: 16px 12px;
      border-top: 1px solid #EDEEF0;
      background-color: #FFF;
      .btn {
        width: 100%;
      }
    }
    &__inner {
      display: block;
      min-height: 0;
      padding: 16px;
    }
    &__close {
      position: absolute;
      top: 12px;
      right: 16px;
      display: block;
      .ic {
        width: 24px;
        height: 24px;
      }
    }
    &__ic {
      width: 62px;
    }
    &__list {
      padding-right: 0;
    }
    &__label {
      position: relative;
      top: 0;
      right: 0;
      display: inline-block;
      margin-bottom: 8px;
    }
  }

  @include media-breakpoint-down(sm) {
    &__pane {
      top: 54px;
      left: 0;
      right: 0;
      height: calc(100vh - 54px);
    }
  }
}



.b-templates-content {
  &__inner {
    padding: 32px;
    border: 1px solid #2F8733;
    border-radius: 6px;
    font-size: 15px;
    line-height: 140%;
    color: #393B3C;
    p {
    }
    ol {
      padding-left: 20px;
    }
  }
  &__title {
    margin-bottom: 16px;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    color: #363540;
  }
  &__subtitle {
    margin-top: 28px;
    margin-bottom: 8px;
    font-weight: 500;
    color: #32333B;
  }
  &__list {
    margin-top: 8px;
    margin-bottom: 0;
    padding-left: 8px;
    li {
      position: relative;
      padding-left: 12px;
      &:after {
        content: '';
        position: absolute;
        left: 1px;
        top: 8px;
        width: 3px;
        height: 3px;
        border-radius: 50%;
        background-color: #393B3C;
      }
    }
  }

  &__example-wrap {
    display: inline-block;
  }
  &__example {
    display: flex;
    align-items: center;
    margin-top: 2px;

    &_mod-a {
      .b-templates-content__example-item {
        width: 70px;
      }
    }
    &_mod-b {
      .b-templates-content__example-item {
        width: auto;
        max-width: 38px;
        &:not(:last-child) {
          margin-right: 5px;
        }
      }
    }
  }
  &__example-item {
    width: 70px;
    text-align: center;
    &:not(:last-child) {
      margin-right: 12px;
    }
  }
  &__example-text {
    display: block;
    margin-top: 6px;
    color: #6C7685;
    font-size: 12px;
    line-height: 125%;
  }
  &__example-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 8px;
    color: var(--grays-500, #6C7685);
    font-size: 12px;
    line-height: 125%;
  }

  img {
    max-width: 100%;
    height: auto;
  }

  @include media-breakpoint-down(lg) {
    &__more {
      display: flex;
      margin-top: 16px;
      margin-left: 12px;
      margin-right: 12px;
      margin-bottom: 6px;
    }
  }

  @include media-breakpoint-down(md) {
    margin-top: 52px;
    overflow-y: auto;
    &__inner {
      margin: 0 12px 12px;
      padding: 16px;
    }
  }
}
