.b-modal-chat {
  .modal-dialog {
    display: flex;
    justify-content: flex-end;
    max-width: 100%;
    height: 100%;
    margin: 0;
    padding-right: 8px;
    padding-top: 12px;
    padding-bottom: 12px;
  }
  .modal-content {
    max-width: 1017px;
    box-shadow: 0 8px 24px rgba(23, 26, 31, 0.08);
    overflow: hidden;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 20px;
    background-color: #599F5C;
    color: #fff;
    .close {
      width: 32px;
      height: 32px;
      margin-left: 12px;
      .ic {
        stroke: #fff;
      }
    }
    .btn {
      padding-top: 6px;
      padding-bottom: 6px;
      font-weight: normal;
    }
  }
  &__header-title {
    font-size: 16px;
    font-weight: 500;
    line-height: 1.4;
  }
  &__header-status {
    font-size: 12px;
    color: #EAF3EB;
  }
  &__header-btn {
    margin-right: 12px;
    padding: 5px 8px;
    border: 1px solid #82B785;
    border-radius: 4px;
    background-color: transparent;
    stroke: #fff;
  }
  &__body {
    display: flex;
    max-height: calc(100vh - 90px);
  }
  &__main {
    flex: 0 0 auto;
    width: calc(100% - 376px);
    max-height: calc(100vh - 90px);
  }
  &__sidebar {
    flex: 0 0 auto;
    width: 376px;
    padding: 16px;
    background-color: #F3F7F3;
  }
  &__sidebar-item {
    margin-bottom: 12px;
    padding-bottom: 8px;
    border-bottom: 1px solid #DADDE1;
  }
  &__sidebar-label {
    display: block;
    margin-bottom: 0;
    font-size: 12px;
    color: #6C7685;
  }
  &__sidebar-input {
    width: 100%;
    border: none;
    background-color: transparent;
    font-weight: 500;
    color: #2B323E;
    &.st-lg {
      font-size: 18px;
      font-weight: 700;
    }
  }
  &__descip {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 10px;
    font-size: 13px;
    dt {
      width: 50%;
      margin-bottom: 6px;
      padding-right: 10px;
      font-weight: normal;
      flex: 0 0 auto;
      color: #6C7685;
    }
    dd {
      width: 50%;
      margin-bottom: 6px;
      flex: 0 0 auto;
      color: #1C2229;
    }
    .ic {
      margin-right: 10px;
      fill: #9198A4;
    }
    .ic-orange {
      fill: #E87902;
      stroke: #E87902;
    }
    .ic-blue {
      fill: #0573C7;
      stroke: #0573C7;
    }

    .ui-btn-null .ic {
      stroke: #9198A4;
    }
  }

  &__list {
    padding-top: 12px;
  }
  &__list-item {
    position: relative;
    padding-bottom: 14px;
    padding-left: 18px;
    &:before,
    &:after {
      content: '';
      position: absolute;
      display: block;
      background-color: #82B785;
    }
    &:before {
      top: calc(50% - 10px);
      width: 1px;
      left: 3px;
      bottom: 0;
    }
    &:after {
      left: 0;
      top: calc(50% - 10px);
      width: 6px;
      height: 6px;
      border-radius: 50%;
    }
    &:first-child {
      .b-modal-chat__list-line {
        display: none;
      }
    }
    &:last-child {
      &:before {
        display: none;
      }
    }
  }
  &__list-line {
    &:after {
      content: '';
      position: absolute;
      top: 0;
      display: block;
      width: 1px;
      left: 3px;
      bottom: calc(50% + 10px);
      background-color: #82B785;
    }
  }
  &__list-title {
    display: block;
    color: #1C2229;
    font-size: 13px;
    font-weight: 500;
  }
  &__list-info {
    display: block;
    font-size: 12px;
    color: #9198A4;
  }


  @include media-breakpoint-down(sm) {
    &__body {
      display: block;
      overflow-y: auto;
    }
    &__main,
    &__sidebar {
      width: 100%;
      max-height: 100%;
    }
    &__sidebar {
      border-top: 2px solid #fff;
    }
  }


  @media (max-width:550px) {
    &__header {
      display: block;
    }
    &__header-status {
      margin-bottom: 10px;
    }
    &__body {
      max-height: calc(100vh - 130px);
    }
  }

}



.b-modal-chat-messenger {
  height: 100%;
  &__body {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  &__list {
    flex-grow: 1;
    padding: 12px 12px 24px;
  }
  &__date {
    padding: 8px 16px;
    border-bottom: 1px solid #F5F7FA;
    font-size: 18px;
    font-weight: 500;
    color: #394352;
  }
  &__item {
    &:not(:first-child) {
      margin-top: 16px;
    }
    &.st-left {
      .b-modal-chat-messenger__info {
        max-width: 320px;
        border-bottom-left-radius: 0;
        background: #599F5C;
        color: #FFF;
      }
      .b-modal-chat-messenger__time {
        color: #D5E7D6;
      }
      + .st-left {
        margin-top: 4px;
      }
    }
    &.st-right {
      text-align: right;
      .b-modal-chat-messenger__info {
        max-width: 355px;
        border-bottom-right-radius: 0;
        background: #EDEEF0;
        color: #1C2229;
      }
      .b-modal-chat-messenger__author {
        justify-content: flex-end;
      }
      .b-modal-chat-messenger__author-photo {
        margin-left: 10px;
        margin-right: 0;
      }
      + .st-right {
        margin-top: 4px;
      }
    }
  }
  &__time {
    display: block;
    margin-bottom: 4px;
    color: var(--grays-600, #475467);
    font-size: 12px;
    line-height: 125%;
  }
  &__author {
    display: flex;
    align-items: center;
    margin-top: 8px;
    color: var(--grays-500, #6C7685);
    font-size: 14px;
    line-height: 125%;
  }
  &__author-photo {
    width: 28px;
    height: 28px;
    margin-right: 10px;
    object-fit: cover;
    border-radius: 50%;
    overflow: hidden;
  }
  &__info {
    display: inline-block;
    max-width: 90%;
    padding: 12px;
    color: var(--grays-800, #2B323E);
    font-size: 15px;
    line-height: 144%;
    border-radius: 12px;
    text-align: left;
  }
  &__footer {
    height: 80px;
    padding: 8px;
    border-top: 1px solid #EAECF0;
    background: #F5F7FA;
    .ui-btn-null {
      .ic {
        stroke: #9198A4;
        fill: #9198A4;
      }
    }
  }
  &__form {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__input {
    height: 40px;
    flex-grow: 1;
    padding: 10px;
    border: none;
    background-color: transparent;

    &::placeholder {
      color: var(--grays-400, #9198A4);
    }
  }
  &__submit {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    margin-left: 4px;
    border: none;
    background-color: transparent;
  }
  &__text {
    padding: 4px;
    text-align: center;
    color: var(--grays-400, #9198A4);
    font-size: 14px;
    line-height: 125%;
  }
  &__editing {
    width: 24px;
    height: 24px;
  }

  &__btn-item {
    &:not(:last-child) {
      margin-bottom: 6px;
    }
  }

  &__btn {
    display: inline-block;
    padding: 6px 12px;
    color: var(--grays-500, #6C7685);
    font-size: 14px;
    line-height: 125%;
    border-radius: 4px;
    border: 1px solid var(--grays-200, #DADDE1);
    background-color: #FFF;
    transition: all .3s;

    &:hover {
      background-color: #F5F7FA;
      border-color: #475467;
      color: #475467;
    }
  }
}
