.b-modal-filter {
  &__content {}
  &__clear {
    padding: 4px;
    border: none;
    background-color: transparent;
    color: var(--main-green-600, #2F8733);
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
    margin-right: 12px;
  }
  &__badge {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 18px;
    height: 18px;
    margin-left: 8px;
    padding: 0 2px;
    border-radius: 4px;
    background: var(--main-green-600, #2F8733);
    color: var(--base-white, #FFF);
    font-size: 12px;
    font-weight: 600;
    line-height: 1;
  }
  &__list {}
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 16px 4px 16px 8px;
    color: var(--grays-600, #475467);
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
    border: none;
    border-bottom: 0.5px solid var(--grays-200, #DADDE1);
    background-color: transparent;
    text-align: left;

    &:after {
      content: '';
      display: block;
      width: 18px;
      height: 18px;
      flex: 0 0 auto;
      background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="none"><path fill-rule="evenodd" clip-rule="evenodd" d="M5.76711 11.8159C5.53743 11.577 5.54488 11.1972 5.78374 10.9675L8.93395 8L5.78374 5.0325C5.54488 4.80282 5.53743 4.423 5.76711 4.18413C5.99679 3.94527 6.37661 3.93782 6.61547 4.1675L10.2155 7.5675C10.3331 7.68062 10.3996 7.83679 10.3996 8C10.3996 8.16321 10.3331 8.31938 10.2155 8.4325L6.61548 11.8325C6.37661 12.0622 5.99679 12.0547 5.76711 11.8159Z" fill="%239198A4"/></svg>');
    }
  }
  &__name {
    flex: 0 0 auto;
    width: calc(50% - 17px);
  }
  &__options {
    flex: 0 0 auto;
    width: calc(50% - 17px);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 14px;
    font-weight: 500;
    line-height: 140%;
    color: var(--grays-800, #2B323E);
    text-align: right;
  }
  &__check {
    padding: 16px 4px 16px 8px;
    border-bottom: 0.5px solid var(--grays-200, #DADDE1);
  }

  .modal-body {
    padding: 8px 12px;
  }
  .modal-title {
    display: flex;
    align-items: center;
    flex-grow: 1;
    margin-right: 20px;
  }
}
