.b-modal-order {
  &__table {
    width: 100%;
    td {
      padding-top: 8px;
      padding-bottom: 8px;
      border-bottom: 1px solid #EDEEF0;
      &:first-child {
        padding-right: 16px;
        color: #475467;
      }
      &:last-child {
        text-align: right;
        font-weight: 500;
      }
    }
  }

  .modal-footer {
    .btn {
      flex-grow: 1;
    }
  }
  .modal-header {
    border-bottom: none;
  }
  .modal-dialog {
    display: flex;
  }


  @include media-breakpoint-up(md) {
    .modal-dialog {
      max-width: 360px;
      height: calc(100vh - 40px);
      margin-right: 20px;
      margin-top: 20px;
      margin-bottom: 20px;
    }
    .modal-body {
      padding-top: 8px;
      overflow-y: auto;
    }
  }
}
