.b-profile {
  &__logo {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 80px;
    height: 80px;
    margin-bottom: 12px;
    border: 1px solid var(--grays-100, #EDEEF0);
    padding: 4px;
    border-radius: 4px;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  &__logo-text {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    font-size: 24px;
    font-weight: 500;
    color: #FFF;
    background-color: #BF6AD4;
    border-radius: 50%;
  }


  @include media-breakpoint-down(sm) {
    &__logo {
      margin-left: auto;
      margin-right: auto;
    }
  }
}
