.b-modal-email {
  &__header {
    padding: 12px 16px;
    border-radius: 10px;
    background: var(--grays-50, #F5F7FA);
    font-size: 15px;
  }
  &__header-item {
    &:not(:first-child) {
      margin-top: 12px;
      padding-top: 12px;
      border-top: 1px solid var(--grays-100, #EDEEF0);
    }
  }
  &__header-title {
    margin-right: 4px;
    font-weight: 500;
  }
  &__item {
    margin-top: 24px;
  }
  &__label {
    margin-bottom: 6px;
  }
  &__list {}
  &__link {
    display: block;
    padding: 16px 16px 18px 16px;
    text-decoration: none;
    color: var(--grays-900, #1C2229);
    border-radius: 6px;
    border: 1px solid var(--grays-200, #DADDE1);
    background: #FFF;
    line-height: 140%;
    transition: all .3s;

    &:not(:last-child) {
      margin-bottom: 12px;
    }
    &:hover {
      border-color: var(--main-green-600, #2F8733);
      color: var(--main-green-700, #266C29);
      text-decoration: none;
    }
  }
  &__name {
    margin-bottom: 4px;
    font-size: 16px;
    font-weight: 500;
  }
  &__text {
    color: var(--grays-600, #475467);
    font-size: 14px;
  }

  .modal-dialog {
    max-width: 620px;
  }
}
