.b-final-2 {
  display: flex;
  justify-content: center;
  padding-top: 36px;
  &__img {
    margin-bottom: 12px;
    text-align: center;
  }
  &__title {
    margin-bottom: 40px;
    color: var(--grays-900, #1C2229);
    text-align: center;
    font-size: 40px;
    font-weight: 500;
    line-height: 124%;
  }
  &__text {
    margin-bottom: 16px;
    color: var(--grays-800, #2B323E);
    text-align: center;
    font-size: 20px;
    line-height: 140%;
    letter-spacing: 0.04px;
  }
  &__group {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }

  .ui-card {
    width: 392px;
    margin-left: 8px;
    margin-right: 8px;
    margin-bottom: 16px;
  }

  @include media-breakpoint-down(md) {
    &__title {
      font-size: 24px;
    }
    .ui-card {
      width: 100%;
      margin-left: 0;
      margin-right: 0;
    }
  }
}
